import React, { createContext, useContext, useEffect, useRef, useState } from 'react';
import io from 'socket.io-client';
import { useAuth } from './AuthContext';

const SocketContext = createContext();

export const SocketProvider = ({ children }) => {
  const { token, isAuthenticated } = useAuth();
  const [socket, setSocket] = useState(null);
  const [isConnected, setIsConnected] = useState(false);
  const [activeUsers, setActiveUsers] = useState([]);
  const [currentRoom, setCurrentRoom] = useState(null);
  const socketRef = useRef(null);

  // Initialize socket connection
  useEffect(() => {
    if (isAuthenticated && token && !socketRef.current) {
      const newSocket = io(process.env.REACT_APP_SERVER_URL || 'http://localhost:5000', {
        auth: {
          token: token
        },
        transports: ['websocket']
      });

      socketRef.current = newSocket;
      setSocket(newSocket);

      // Connection event handlers
      newSocket.on('connect', () => {
        console.log('Socket connected:', newSocket.id);
        setIsConnected(true);
      });

      newSocket.on('disconnect', (reason) => {
        console.log('Socket disconnected:', reason);
        setIsConnected(false);
      });

      newSocket.on('connect_error', (error) => {
        console.error('Socket connection error:', error);
        setIsConnected(false);
      });

      // User presence handlers
      newSocket.on('userOnline', (data) => {
        setActiveUsers(prev => {
          const exists = prev.find(user => user.userId === data.userId);
          if (!exists) {
            return [...prev, data];
          }
          return prev;
        });
      });

      newSocket.on('userOffline', (data) => {
        setActiveUsers(prev => prev.filter(user => user.userId !== data.userId));
      });

      newSocket.on('userJoinedProject', (data) => {
        console.log('User joined project:', data);
      });

      newSocket.on('userLeftProject', (data) => {
        console.log('User left project:', data);
      });

      // Error handling
      newSocket.on('error', (error) => {
        console.error('Socket error:', error);
      });

      return () => {
        newSocket.close();
        socketRef.current = null;
        setSocket(null);
        setIsConnected(false);
        setActiveUsers([]);
        setCurrentRoom(null);
      };
    }
  }, [isAuthenticated, token]);

  // Cleanup on logout
  useEffect(() => {
    if (!isAuthenticated && socketRef.current) {
      socketRef.current.close();
      socketRef.current = null;
      setSocket(null);
      setIsConnected(false);
      setActiveUsers([]);
      setCurrentRoom(null);
    }
  }, [isAuthenticated]);

  // Join project room
  const joinProject = (projectId) => {
    return new Promise((resolve, reject) => {
      if (!socket || !isConnected) {
        reject(new Error('Socket not connected'));
        return;
      }

      socket.emit('joinProject', { projectId }, (response) => {
        if (response.error) {
          reject(new Error(response.error));
        } else {
          setCurrentRoom(response.roomId);
          resolve(response);
        }
      });
    });
  };

  // Leave current room
  const leaveRoom = () => {
    if (socket && currentRoom) {
      // Socket.io automatically handles leaving rooms on disconnect
      setCurrentRoom(null);
    }
  };

  // Whiteboard event handlers
  const emitWhiteboardEvent = (eventType, data) => {
    if (socket && isConnected) {
      socket.emit(`whiteboard:${eventType}`, data);
    }
  };

  const onWhiteboardEvent = (eventType, callback) => {
    if (socket) {
      socket.on(`whiteboard:${eventType}`, callback);
    }
  };

  const offWhiteboardEvent = (eventType, callback) => {
    if (socket) {
      socket.off(`whiteboard:${eventType}`, callback);
    }
  };

  // Chat event handlers
  const sendChatMessage = (projectId, message, type = 'text') => {
    if (socket && isConnected) {
      socket.emit('chat:sendMessage', {
        projectId,
        message,
        type
      });
    }
  };

  const onChatMessage = (callback) => {
    if (socket) {
      socket.on('chat:newMessage', callback);
    }
  };

  const offChatMessage = (callback) => {
    if (socket) {
      socket.off('chat:newMessage', callback);
    }
  };

  const emitTyping = (projectId, isTyping) => {
    if (socket && isConnected) {
      socket.emit('chat:typing', { projectId, isTyping });
    }
  };

  const onUserTyping = (callback) => {
    if (socket) {
      socket.on('chat:userTyping', callback);
    }
  };

  const offUserTyping = (callback) => {
    if (socket) {
      socket.off('chat:userTyping', callback);
    }
  };

  // Task event handlers
  const emitTaskUpdate = (taskData) => {
    if (socket && isConnected) {
      socket.emit('task:update', taskData);
    }
  };

  const emitTaskMove = (taskData) => {
    if (socket && isConnected) {
      socket.emit('task:move', taskData);
    }
  };

  const onTaskUpdate = (callback) => {
    if (socket) {
      socket.on('task:updated', callback);
    }
  };

  const onTaskMove = (callback) => {
    if (socket) {
      socket.on('task:moved', callback);
    }
  };

  const offTaskUpdate = (callback) => {
    if (socket) {
      socket.off('task:updated', callback);
    }
  };

  const offTaskMove = (callback) => {
    if (socket) {
      socket.off('task:moved', callback);
    }
  };

  // Document collaboration handlers
  const emitDocumentEdit = (documentData) => {
    if (socket && isConnected) {
      socket.emit('document:edit', documentData);
    }
  };

  const emitCursorPosition = (position) => {
    if (socket && isConnected) {
      socket.emit('document:cursorPosition', position);
    }
  };

  const onDocumentEdit = (callback) => {
    if (socket) {
      socket.on('document:edited', callback);
    }
  };

  const onCursorMove = (callback) => {
    if (socket) {
      socket.on('document:cursorMoved', callback);
    }
  };

  const offDocumentEdit = (callback) => {
    if (socket) {
      socket.off('document:edited', callback);
    }
  };

  const offCursorMove = (callback) => {
    if (socket) {
      socket.off('document:cursorMoved', callback);
    }
  };

  // Idea collaboration handlers
  const emitIdeaVote = (ideaData) => {
    if (socket && isConnected) {
      socket.emit('idea:vote', ideaData);
    }
  };

  const onIdeaVote = (callback) => {
    if (socket) {
      socket.on('idea:voted', callback);
    }
  };

  const offIdeaVote = (callback) => {
    if (socket) {
      socket.off('idea:voted', callback);
    }
  };

  // Activity tracking
  const emitActivity = (activity) => {
    if (socket && isConnected) {
      socket.emit('activity:update', { activity });
    }
  };

  const onUserActivity = (callback) => {
    if (socket) {
      socket.on('user:activity', callback);
    }
  };

  const offUserActivity = (callback) => {
    if (socket) {
      socket.off('user:activity', callback);
    }
  };

  const value = {
    socket,
    isConnected,
    activeUsers,
    currentRoom,
    
    // Room management
    joinProject,
    leaveRoom,
    
    // Whiteboard
    emitWhiteboardEvent,
    onWhiteboardEvent,
    offWhiteboardEvent,
    
    // Chat
    sendChatMessage,
    onChatMessage,
    offChatMessage,
    emitTyping,
    onUserTyping,
    offUserTyping,
    
    // Tasks
    emitTaskUpdate,
    emitTaskMove,
    onTaskUpdate,
    onTaskMove,
    offTaskUpdate,
    offTaskMove,
    
    // Documents
    emitDocumentEdit,
    emitCursorPosition,
    onDocumentEdit,
    onCursorMove,
    offDocumentEdit,
    offCursorMove,
    
    // Ideas
    emitIdeaVote,
    onIdeaVote,
    offIdeaVote,
    
    // Activity
    emitActivity,
    onUserActivity,
    offUserActivity
  };

  return (
    <SocketContext.Provider value={value}>
      {children}
    </SocketContext.Provider>
  );
};

export const useSocket = () => {
  const context = useContext(SocketContext);
  if (!context) {
    throw new Error('useSocket must be used within a SocketProvider');
  }
  return context;
};
