import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useSocket } from '../contexts/SocketContext';
import axios from 'axios';
import './Chat.css';

// Mock projects for chat channels
const mockProjects = [
  { _id: 'general', name: 'General', type: 'general' },
  { _id: 'project1', name: 'Website Redesign', type: 'project' },
  { _id: 'project2', name: 'Mobile App', type: 'project' },
  { _id: 'project3', name: 'Brand Identity', type: 'project' }
];

const Chat = () => {
  const { user } = useAuth();
  const { 
    sendChatMessage, 
    onChatMessage, 
    offChatMessage,
    emitTyping,
    onUserTyping,
    offUserTyping,
    isConnected,
    activeUsers 
  } = useSocket();
  
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [typingUsers, setTypingUsers] = useState([]);
  const [selectedProject, setSelectedProject] = useState('general');
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showEmojiPicker, setShowEmojiPicker] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  
  const messagesEndRef = useRef(null);
  const inputRef = useRef(null);
  const typingTimeoutRef = useRef(null);

  const emojis = ['😀', '😂', '😍', '🤔', '👍', '👎', '❤️', '🎉', '🔥', '💯', '🚀', '💡'];

  const loadProjects = useCallback(() => {
    setProjects(mockProjects);
  }, []);

  const loadMessages = useCallback(async () => {
    if (!selectedProject) return;

    try {
      setLoading(true);
      const response = await axios.get(`/api/chat/${selectedProject}`);
      setMessages(response.data);
    } catch (error) {
      console.error('Error loading messages:', error);
      // Mock messages for development
      const mockMessages = [
        {
          _id: '1',
          content: 'Hey everyone! Welcome to the project chat 👋',
          sender: { _id: 'user1', name: 'Sarah Johnson', avatar: null },
          projectId: selectedProject,
          type: 'text',
          createdAt: new Date(Date.now() - 1000 * 60 * 60 * 2).toISOString(),
          reactions: [
            { emoji: '👋', users: [{ _id: 'user2', name: 'Mike Chen' }] }
          ]
        },
        {
          _id: '2',
          content: 'Thanks Sarah! Excited to work on this project.',
          sender: { _id: 'user2', name: 'Mike Chen', avatar: null },
          projectId: selectedProject,
          type: 'text',
          createdAt: new Date(Date.now() - 1000 * 60 * 60 * 1).toISOString(),
          reactions: []
        },
        {
          _id: '3',
          content: 'I\'ve uploaded the initial wireframes to the documents section. Please take a look and share your feedback!',
          sender: { _id: 'user3', name: 'Alex Rivera', avatar: null },
          projectId: selectedProject,
          type: 'text',
          createdAt: new Date(Date.now() - 1000 * 60 * 30).toISOString(),
          reactions: [
            { emoji: '👍', users: [{ _id: 'user1', name: 'Sarah Johnson' }, { _id: 'user2', name: 'Mike Chen' }] }
          ]
        }
      ];
      setMessages(mockMessages);
    } finally {
      setLoading(false);
    }
  }, [selectedProject]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    loadProjects();
    if (selectedProject) {
      loadMessages();
    }
    
    // Set up real-time chat
    onChatMessage((data) => {
      if (data.projectId === selectedProject) {
        setMessages(prev => [...prev, data.message]);
        scrollToBottom();
      }
    });

    onUserTyping((data) => {
      if (data.projectId === selectedProject && data.userId !== user?._id) {
        setTypingUsers(prev => {
          if (data.isTyping) {
            return [...prev.filter(u => u.userId !== data.userId), data];
          } else {
            return prev.filter(u => u.userId !== data.userId);
          }
        });
      }
    });

    return () => {
      offChatMessage();
      offUserTyping();
    };
  }, [selectedProject, loadMessages, loadProjects, onChatMessage, offChatMessage, onUserTyping, offUserTyping, user?._id]);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSendMessage = async (e) => {
    e.preventDefault();
    if (!newMessage.trim()) return;

    const messageData = {
      content: newMessage,
      type: 'text',
      projectId: selectedProject,
      sender: user
    };

    // Add message to local state immediately
    const localMessage = {
      _id: Date.now().toString(),
      ...messageData,
      createdAt: new Date().toISOString(),
      reactions: []
    };
    
    setMessages(prev => [...prev, localMessage]);
    setNewMessage('');
    
    // Clear typing indicator
    if (isTyping) {
      setIsTyping(false);
      emitTyping(selectedProject, false);
    }

    // Send to server and real-time
    try {
      sendChatMessage(selectedProject, newMessage);
      await axios.post('/api/chat', messageData);
    } catch (error) {
      console.error('Error sending message:', error);
    }
  };

  const handleInputChange = (e) => {
    setNewMessage(e.target.value);
    
    // Handle typing indicator
    if (!isTyping) {
      setIsTyping(true);
      emitTyping(selectedProject, true);
    }

    // Clear previous timeout
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current);
    }

    // Set new timeout to stop typing indicator
    typingTimeoutRef.current = setTimeout(() => {
      setIsTyping(false);
      emitTyping(selectedProject, false);
    }, 1000);
  };

  const handleEmojiClick = (emoji) => {
    setNewMessage(prev => prev + emoji);
    setShowEmojiPicker(false);
    inputRef.current?.focus();
  };

  const handleReaction = async (messageId, emoji) => {
    try {
      await axios.post(`/api/chat/${messageId}/react`, { emoji });
      
      // Update local state
      setMessages(prev => prev.map(msg => {
        if (msg._id === messageId) {
          const existingReaction = msg.reactions.find(r => r.emoji === emoji);
          if (existingReaction) {
            const userIndex = existingReaction.users.findIndex(u => u._id === user._id);
            if (userIndex >= 0) {
              // Remove user's reaction
              existingReaction.users.splice(userIndex, 1);
              if (existingReaction.users.length === 0) {
                msg.reactions = msg.reactions.filter(r => r.emoji !== emoji);
              }
            } else {
              // Add user's reaction
              existingReaction.users.push({ _id: user._id, name: user.name });
            }
          } else {
            // Add new reaction
            msg.reactions.push({
              emoji,
              users: [{ _id: user._id, name: user.name }]
            });
          }
        }
        return msg;
      }));
    } catch (error) {
      console.error('Error adding reaction:', error);
    }
  };

  const formatTime = (timestamp) => {
    const date = new Date(timestamp);
    const now = new Date();
    const diffInHours = (now - date) / (1000 * 60 * 60);
    
    if (diffInHours < 24) {
      return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    } else {
      return date.toLocaleDateString();
    }
  };

  const isConsecutiveMessage = (currentMsg, prevMsg) => {
    if (!prevMsg) return false;
    
    const timeDiff = new Date(currentMsg.createdAt) - new Date(prevMsg.createdAt);
    const isSameSender = currentMsg.sender._id === prevMsg.sender._id;
    const isWithinFiveMinutes = timeDiff < 5 * 60 * 1000;
    
    return isSameSender && isWithinFiveMinutes;
  };

  const filteredMessages = messages.filter(message =>
    message.content.toLowerCase().includes(searchTerm.toLowerCase()) ||
    message.sender.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  if (loading) {
    return (
      <div className="chat-loading">
        <div className="loading-spinner large"></div>
        <p>Loading chat...</p>
      </div>
    );
  }

  return (
    <div className="chat-container">
      <div className="chat-sidebar">
        <div className="sidebar-header">
          <h3>Chat Channels</h3>
          <div className="connection-status">
            <div className={`status-dot ${isConnected ? 'connected' : 'disconnected'}`}></div>
            {isConnected ? 'Online' : 'Offline'}
          </div>
        </div>

        <div className="channels-list">
          {projects.map(project => (
            <button
              key={project._id}
              className={`channel-item ${selectedProject === project._id ? 'active' : ''}`}
              onClick={() => setSelectedProject(project._id)}
            >
              <div className="channel-icon">
                {project.type === 'general' ? '💬' : '📁'}
              </div>
              <span className="channel-name">{project.name}</span>
              {project._id !== 'general' && (
                <div className="project-indicator"></div>
              )}
            </button>
          ))}
        </div>

        <div className="online-users">
          <h4>Online Users ({activeUsers.length})</h4>
          <div className="users-list">
            {activeUsers.map(user => (
              <div key={user.userId} className="user-item">
                <div className="user-avatar">
                  {user.name?.charAt(0).toUpperCase()}
                </div>
                <div className="user-info">
                  <span className="user-name">{user.name}</span>
                  <div className="user-status online">Online</div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      <div className="chat-main">
        <div className="chat-header">
          <div className="channel-info">
            <h2>
              {projects.find(p => p._id === selectedProject)?.name || 'Channel'}
            </h2>
            <p>{filteredMessages.length} messages</p>
          </div>

          <div className="chat-actions">
            <div className="search-box">
              <input
                type="text"
                placeholder="Search messages..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
              <span className="search-icon">🔍</span>
            </div>
          </div>
        </div>

        <div className="messages-container">
          <div className="messages-list">
            {filteredMessages.map((message, index) => {
              const prevMessage = filteredMessages[index - 1];
              const isConsecutive = isConsecutiveMessage(message, prevMessage);
              const isOwnMessage = message.sender._id === user?._id;

              return (
                <div
                  key={message._id}
                  className={`message ${isOwnMessage ? 'own-message' : ''} ${isConsecutive ? 'consecutive' : ''}`}
                >
                  {!isConsecutive && (
                    <div className="message-header">
                      <div className="sender-avatar">
                        {message.sender.avatar ? (
                          <img src={message.sender.avatar} alt="" />
                        ) : (
                          message.sender.name.charAt(0).toUpperCase()
                        )}
                      </div>
                      <div className="message-meta">
                        <span className="sender-name">{message.sender.name}</span>
                        <span className="message-time">{formatTime(message.createdAt)}</span>
                      </div>
                    </div>
                  )}

                  <div className="message-content">
                    <p>{message.content}</p>
                    
                    {message.reactions.length > 0 && (
                      <div className="message-reactions">
                        {message.reactions.map(reaction => (
                          <button
                            key={reaction.emoji}
                            className={`reaction ${reaction.users.some(u => u._id === user?._id) ? 'own-reaction' : ''}`}
                            onClick={() => handleReaction(message._id, reaction.emoji)}
                            title={reaction.users.map(u => u.name).join(', ')}
                          >
                            {reaction.emoji} {reaction.users.length}
                          </button>
                        ))}
                      </div>
                    )}
                  </div>

                  <div className="message-actions">
                    <div className="quick-reactions">
                      {['👍', '❤️', '😂', '🎉'].map(emoji => (
                        <button
                          key={emoji}
                          className="quick-reaction"
                          onClick={() => handleReaction(message._id, emoji)}
                          title={`React with ${emoji}`}
                        >
                          {emoji}
                        </button>
                      ))}
                    </div>
                  </div>
                </div>
              );
            })}
            
            {typingUsers.length > 0 && (
              <div className="typing-indicator">
                <div className="typing-avatars">
                  {typingUsers.map(user => (
                    <div key={user.userId} className="typing-avatar">
                      {user.userName?.charAt(0).toUpperCase()}
                    </div>
                  ))}
                </div>
                <div className="typing-text">
                  {typingUsers.length === 1 
                    ? `${typingUsers[0].userName} is typing...`
                    : `${typingUsers.length} people are typing...`
                  }
                </div>
                <div className="typing-dots">
                  <span></span>
                  <span></span>
                  <span></span>
                </div>
              </div>
            )}
            
            <div ref={messagesEndRef} />
          </div>
        </div>

        <div className="chat-input">
          <form onSubmit={handleSendMessage} className="message-form">
            <div className="input-container">
              <button
                type="button"
                className="emoji-btn"
                onClick={() => setShowEmojiPicker(!showEmojiPicker)}
              >
                😀
              </button>
              
              <input
                ref={inputRef}
                type="text"
                value={newMessage}
                onChange={handleInputChange}
                placeholder={`Message ${projects.find(p => p._id === selectedProject)?.name || 'channel'}...`}
                disabled={!isConnected}
              />
              
              <button type="submit" disabled={!newMessage.trim() || !isConnected}>
                <span className="send-icon">📤</span>
              </button>
            </div>

            {showEmojiPicker && (
              <div className="emoji-picker">
                {emojis.map(emoji => (
                  <button
                    key={emoji}
                    type="button"
                    className="emoji-option"
                    onClick={() => handleEmojiClick(emoji)}
                  >
                    {emoji}
                  </button>
                ))}
              </div>
            )}
          </form>
        </div>
      </div>
    </div>
  );
};

export default Chat;
