import React, { useState } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import './Navbar.css';

const Navbar = () => {
  const { user, logout, isAuthenticated } = useAuth();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isProfileDropdownOpen, setIsProfileDropdownOpen] = useState(false);
  const location = useLocation();
  const navigate = useNavigate();

  const handleLogout = async () => {
    try {
      await logout();
      navigate('/login');
    } catch (error) {
      console.error('Logout error:', error);
    }
  };

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const toggleProfileDropdown = () => {
    setIsProfileDropdownOpen(!isProfileDropdownOpen);
  };

  const isActiveRoute = (path) => {
    return location.pathname === path;
  };

  if (!isAuthenticated) {
    return null; // Don't show navbar on login/register pages
  }

  return (
    <nav className="navbar">
      <div className="navbar-container">
        {/* Logo */}
        <div className="navbar-brand">
          <Link to="/dashboard" className="brand-link">
            <div className="brand-icon">🚀</div>
            <span className="brand-text">CollabAI</span>
          </Link>
        </div>

        {/* Mobile menu button */}
        <button 
          className="mobile-menu-btn"
          onClick={toggleMenu}
          aria-label="Toggle menu"
        >
          <span className={`hamburger ${isMenuOpen ? 'active' : ''}`}>
            <span></span>
            <span></span>
            <span></span>
          </span>
        </button>

        {/* Navigation Links */}
        <div className={`navbar-menu ${isMenuOpen ? 'active' : ''}`}>
          <div className="navbar-nav">
            <Link 
              to="/dashboard" 
              className={`nav-link ${isActiveRoute('/dashboard') ? 'active' : ''}`}
              onClick={() => setIsMenuOpen(false)}
            >
              <span className="nav-icon">📊</span>
              Dashboard
            </Link>
            
            <Link 
              to="/projects" 
              className={`nav-link ${isActiveRoute('/projects') ? 'active' : ''}`}
              onClick={() => setIsMenuOpen(false)}
            >
              <span className="nav-icon">📁</span>
              Projects
            </Link>
            
                        <Link 
              to="/kanban" 
              className={`nav-link ${isActiveRoute('/kanban') ? 'active' : ''}`}
              onClick={() => setIsMenuOpen(false)}
            >
              <span className="nav-icon">📋</span>
              <span className="nav-text">Tasks</span>
            </Link>
            
            <Link 
              to="/whiteboard" 
              className={`nav-link ${isActiveRoute('/whiteboard') ? 'active' : ''}`}
              onClick={() => setIsMenuOpen(false)}
            >
              <span className="nav-icon">🎨</span>
              Whiteboard
            </Link>
            
            <Link 
              to="/chat" 
              className={`nav-link ${isActiveRoute('/chat') ? 'active' : ''}`}
              onClick={() => setIsMenuOpen(false)}
            >
              <span className="nav-icon">💬</span>
              Chat
            </Link>
            
            <Link 
              to="/documents" 
              className={`nav-link ${isActiveRoute('/documents') ? 'active' : ''}`}
              onClick={() => setIsMenuOpen(false)}
            >
              <span className="nav-icon">📄</span>
              Documents
            </Link>
            
            {(user?.role === 'admin' || user?.role === 'project_manager') && (
              <Link 
                to="/analytics" 
                className={`nav-link ${isActiveRoute('/analytics') ? 'active' : ''}`}
                onClick={() => setIsMenuOpen(false)}
              >
                <span className="nav-icon">📈</span>
                Analytics
              </Link>
            )}
          </div>

          {/* User Profile */}
          <div className="navbar-user">
            <div className="user-profile-dropdown">
              <button 
                className="profile-btn"
                onClick={toggleProfileDropdown}
                aria-label="User menu"
              >
                <div className="user-avatar">
                  {user?.profilePicture ? (
                    <img src={user.profilePicture} alt="Profile" />
                  ) : (
                    <span className="avatar-initial">
                      {user?.name?.charAt(0).toUpperCase() || 'U'}
                    </span>
                  )}
                </div>
                <span className="user-name">{user?.name || 'User'}</span>
                <span className={`dropdown-arrow ${isProfileDropdownOpen ? 'up' : 'down'}`}>
                  ▼
                </span>
              </button>

              {isProfileDropdownOpen && (
                <div className="profile-dropdown-menu">
                  <div className="dropdown-header">
                    <div className="user-info">
                      <p className="user-email">{user?.email}</p>
                      <p className="user-role">{user?.role}</p>
                    </div>
                  </div>
                  
                  <div className="dropdown-divider"></div>
                  
                  <Link 
                    to="/profile" 
                    className="dropdown-item"
                    onClick={() => {
                      setIsProfileDropdownOpen(false);
                      setIsMenuOpen(false);
                    }}
                  >
                    <span className="dropdown-icon">👤</span>
                    Profile Settings
                  </Link>
                  
                  <Link 
                    to="/preferences" 
                    className="dropdown-item"
                    onClick={() => {
                      setIsProfileDropdownOpen(false);
                      setIsMenuOpen(false);
                    }}
                  >
                    <span className="dropdown-icon">⚙️</span>
                    Preferences
                  </Link>
                  
                  {user?.role === 'admin' && (
                    <Link 
                      to="/admin" 
                      className="dropdown-item"
                      onClick={() => {
                        setIsProfileDropdownOpen(false);
                        setIsMenuOpen(false);
                      }}
                    >
                      <span className="dropdown-icon">🔧</span>
                      Admin Panel
                    </Link>
                  )}
                  
                  <div className="dropdown-divider"></div>
                  
                  <button 
                    className="dropdown-item logout-btn"
                    onClick={() => {
                      setIsProfileDropdownOpen(false);
                      setIsMenuOpen(false);
                      handleLogout();
                    }}
                  >
                    <span className="dropdown-icon">🚪</span>
                    Logout
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Overlay for mobile menu */}
      {isMenuOpen && <div className="navbar-overlay" onClick={() => setIsMenuOpen(false)}></div>}
    </nav>
  );
};

export default Navbar;
