const Analytics = () => {
  const { user } = useAuth();
  const [timeRange, setTimeRange] = useState('7d');
  const [selectedProject, setSelectedProject] = useState('all');
  const [analytics, setAnalytics] = useState({});
  const [loading, setLoading] = useState(true);

  const projects = [
    { id: 'all', name: 'All Projects' },
    { id: 'alpha', name: 'Project Alpha' },
    { id: 'beta', name: 'Project Beta' },
    { id: 'gamma', name: 'Project Gamma' },
    { id: 'delta', name: 'Project Delta' },
    { id: 'echo', name: 'Project Echo' }
  ];

  useEffect(() => {
    // Simulate loading analytics data
    setLoading(true);
    setTimeout(() => {
      setAnalytics(mockAnalytics);
      setLoading(false);
    }, 1000);
  }, [timeRange, selectedProject]);

  const renderChart = (type, data) => {
    // Simple chart simulation using CSS
    switch (type) {
      case 'bar':
        return (
          <div className="chart-container">
            <div className="chart-bars">
              {data.datasets[0].data.map((value, index) => (
                <div key={index} className="bar-group">
                  <div 
                    className="bar bar-primary"
                    style={{ height: `${(value / Math.max(...data.datasets[0].data)) * 100}%` }}
                  ></div>
                  {data.datasets[1] && (
                    <div 
                      className="bar bar-secondary"
                      style={{ height: `${(data.datasets[1].data[index] / Math.max(...data.datasets[1].data)) * 100}%` }}
                    ></div>
                  )}
                  <span className="bar-label">{data.labels[index]}</span>
                </div>
              ))}
            </div>
            <div className="chart-legend">
              {data.datasets.map((dataset, index) => (
                <div key={index} className="legend-item">
                  <div className={`legend-color ${index === 0 ? 'primary' : 'secondary'}`}></div>
                  <span>{dataset.label}</span>
                </div>
              ))}
            </div>
          </div>
        );

      case 'line':
        return (
          <div className="chart-container">
            <div className="line-chart">
              <svg viewBox="0 0 300 150" className="line-svg">
                <polyline
                  points={data.datasets[0].data.map((value, index) => 
                    `${(index * 300) / (data.datasets[0].data.length - 1)},${150 - (value / Math.max(...data.datasets[0].data)) * 120}`
                  ).join(' ')}
                  fill="none"
                  stroke="rgba(102, 126, 234, 1)"
                  strokeWidth="2"
                />
                {data.datasets[0].data.map((value, index) => (
                  <circle
                    key={index}
                    cx={(index * 300) / (data.datasets[0].data.length - 1)}
                    cy={150 - (value / Math.max(...data.datasets[0].data)) * 120}
                    r="4"
                    fill="rgba(102, 126, 234, 1)"
                  />
                ))}
              </svg>
            </div>
            <div className="chart-labels">
              {data.labels.map((label, index) => (
                <span key={index} className="label">{label}</span>
              ))}
            </div>
          </div>
        );

      case 'doughnut':
        const total = data.datasets[0].data.reduce((sum, value) => sum + value, 0);
        let currentAngle = 0;
        return (
          <div className="chart-container">
            <div className="doughnut-chart">
              <svg viewBox="0 0 200 200" className="doughnut-svg">
                {data.datasets[0].data.map((value, index) => {
                  const angle = (value / total) * 360;
                  const startAngle = currentAngle;
                  const endAngle = currentAngle + angle;
                  currentAngle += angle;

                  const x1 = 100 + 70 * Math.cos((startAngle - 90) * Math.PI / 180);
                  const y1 = 100 + 70 * Math.sin((startAngle - 90) * Math.PI / 180);
                  const x2 = 100 + 70 * Math.cos((endAngle - 90) * Math.PI / 180);
                  const y2 = 100 + 70 * Math.sin((endAngle - 90) * Math.PI / 180);

                  const largeArcFlag = angle > 180 ? 1 : 0;

                  return (
                    <path
                      key={index}
                      d={`M 100 100 L ${x1} ${y1} A 70 70 0 ${largeArcFlag} 1 ${x2} ${y2} Z`}
                      fill={data.datasets[0].backgroundColor[index]}
                    />
                  );
                })}
                <circle cx="100" cy="100" r="40" fill="white" />
              </svg>
            </div>
            <div className="chart-legend">
              {data.labels.map((label, index) => (
                <div key={index} className="legend-item">
                  <div 
                    className="legend-color" 
                    style={{ backgroundColor: data.datasets[0].backgroundColor[index] }}
                  ></div>
                  <span>{label}: {data.datasets[0].data[index]}</span>
                </div>
              ))}
            </div>
          </div>
        );

      default:
        return <div>Chart type not supported</div>;
    }
  };

  if (loading) {
    return (
      <div className="analytics-loading">
        <div className="loading-spinner"></div>
        <p>Loading analytics...</p>
      </div>
    );
  }

  return (
    <div className="analytics-dashboard">
      {/* Header */}
      <div className="analytics-header">
        <div className="header-left">
          <h1>Analytics Dashboard</h1>
          <p>Track your team's performance and project progress</p>
        </div>
        
        <div className="header-controls">
          <select 
            value={selectedProject}
            onChange={(e) => setSelectedProject(e.target.value)}
            className="project-filter"
          >
            {projects.map(project => (
              <option key={project.id} value={project.id}>
                {project.name}
              </option>
            ))}
          </select>
          
          <select 
            value={timeRange}
            onChange={(e) => setTimeRange(e.target.value)}
            className="time-filter"
          >
            <option value="7d">Last 7 days</option>
            <option value="30d">Last 30 days</option>
            <option value="90d">Last 3 months</option>
            <option value="1y">Last year</option>
          </select>
        </div>
      </div>

      {/* Overview Cards */}
      <div className="overview-cards">
        <div className="overview-card">
          <div className="card-icon projects">📊</div>
          <div className="card-content">
            <h3>{analytics.overview?.totalProjects}</h3>
            <p>Total Projects</p>
            <span className="trend positive">+12% from last month</span>
          </div>
        </div>

        <div className="overview-card">
          <div className="card-icon tasks">✅</div>
          <div className="card-content">
            <h3>{analytics.overview?.completedTasks}</h3>
            <p>Tasks Completed</p>
            <span className="trend positive">+8% from last month</span>
          </div>
        </div>

        <div className="overview-card">
          <div className="card-icon team">👥</div>
          <div className="card-content">
            <h3>{analytics.overview?.teamMembers}</h3>
            <p>Team Members</p>
            <span className="trend neutral">No change</span>
          </div>
        </div>

        <div className="overview-card">
          <div className="card-icon time">⏱️</div>
          <div className="card-content">
            <h3>{analytics.overview?.avgCompletionTime} days</h3>
            <p>Avg. Completion Time</p>
            <span className="trend negative">+0.5 days from last month</span>
          </div>
        </div>
      </div>

      {/* Charts Section */}
      <div className="charts-grid">
        {/* Task Completion Trend */}
        <div className="chart-card large">
          <div className="chart-header">
            <h3>Task Completion Trend</h3>
            <p>Daily task completion over the selected period</p>
          </div>
          <div className="chart-body">
            {renderChart('line', analytics.taskCompletion)}
          </div>
        </div>

        {/* Task Distribution */}
        <div className="chart-card">
          <div className="chart-header">
            <h3>Task Distribution</h3>
            <p>Current task status breakdown</p>
          </div>
          <div className="chart-body">
            {renderChart('doughnut', analytics.taskDistribution)}
          </div>
        </div>

        {/* Project Performance */}
        <div className="chart-card large">
          <div className="chart-header">
            <h3>Project Performance</h3>
            <p>Completed vs pending tasks by project</p>
          </div>
          <div className="chart-body">
            {renderChart('bar', analytics.projectStats)}
          </div>
        </div>

        {/* Time Tracking */}
        <div className="chart-card">
          <div className="chart-header">
            <h3>Time Distribution</h3>
            <p>Time spent by activity type</p>
          </div>
          <div className="chart-body">
            {renderChart('doughnut', analytics.timeTracking)}
          </div>
        </div>
      </div>

      {/* Detailed Sections */}
      <div className="details-grid">
        {/* Team Performance */}
        <div className="detail-card">
          <div className="detail-header">
            <h3>Team Performance</h3>
            <p>Individual team member statistics</p>
          </div>
          <div className="performance-list">
            {analytics.teamPerformance?.map((member, index) => (
              <div key={index} className="performance-item">
                <div className="member-info">
                  <div className="member-avatar">{member.avatar}</div>
                  <div className="member-details">
                    <h4>{member.name}</h4>
                    <p>{member.tasksCompleted} tasks completed</p>
                  </div>
                </div>
                <div className="performance-metrics">
                  <div className="efficiency-score">
                    <span className="score">{member.efficiency}%</span>
                    <div className="efficiency-bar">
                      <div 
                        className="efficiency-fill"
                        style={{ width: `${member.efficiency}%` }}
                      ></div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Project Progress */}
        <div className="detail-card">
          <div className="detail-header">
            <h3>Project Progress</h3>
            <p>Current status of active projects</p>
          </div>
          <div className="progress-list">
            {analytics.projectProgress?.map((project, index) => (
              <div key={index} className="progress-item">
                <div className="project-info">
                  <h4>{project.name}</h4>
                  <p>Due: {project.dueDate}</p>
                </div>
                <div className="progress-metrics">
                  <div className="progress-bar">
                    <div 
                      className="progress-fill"
                      style={{ width: `${project.progress}%` }}
                    ></div>
                  </div>
                  <span className="progress-value">{project.progress}%</span>
                  <span className={`status-badge ${project.status}`}>
                    {project.status.replace('-', ' ')}
                  </span>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Recent Activity */}
        <div className="detail-card full-width">
          <div className="detail-header">
            <h3>Recent Activity</h3>
            <p>Latest team activities and updates</p>
          </div>
          <div className="activity-list">
            {analytics.recentActivity?.map((activity) => (
              <div key={activity.id} className="activity-item">
                <div className={`activity-icon ${activity.type}`}>
                  {activity.type === 'task' && '✅'}
                  {activity.type === 'project' && '📊'}
                  {activity.type === 'comment' && '💬'}
                  {activity.type === 'update' && '🔄'}
                  {activity.type === 'file' && '📎'}
                </div>
                <div className="activity-content">
                  <p>
                    <strong>{activity.user}</strong> {activity.action}{' '}
                    <span className="activity-target">"{activity.target}"</span>
                  </p>
                  <span className="activity-time">{activity.time}</span>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Export Options */}
      <div className="analytics-footer">
        <div className="export-section">
          <h4>Export Analytics</h4>
          <div className="export-buttons">
            <button className="export-btn">
              📊 Export to Excel
            </button>
            <button className="export-btn">
              📄 Generate PDF Report
            </button>
            <button className="export-btn">
              📧 Email Report
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Analytics;
