import React, { useState, useEffect, useRef } from 'react';
import { Stage, Layer, Line, Rect, Circle, Text, Transformer } from 'react-konva';
import { useAuth } from '../contexts/AuthContext';
import { useSocket } from '../contexts/SocketContext';
import './Whiteboard.css';

const Whiteboard = () => {
  const { user } = useAuth();
  const { 
    emitWhiteboardEvent, 
    onWhiteboardEvent, 
    offWhiteboardEvent,
    isConnected,
    activeUsers 
  } = useSocket();
  
  const stageRef = useRef();
  const [tool, setTool] = useState('pen');
  const [isDrawing, setIsDrawing] = useState(false);
  const [shapes, setShapes] = useState([]);
  const [selectedShapeId, setSelectedShapeId] = useState(null);
  const [history, setHistory] = useState([[]]);
  const [historyStep, setHistoryStep] = useState(0);
  
  // Drawing settings
  const [strokeColor, setStrokeColor] = useState('#000000');
  const [strokeWidth, setStrokeWidth] = useState(2);
  const [fillColor, setFillColor] = useState('transparent');
  
  // Text input
  const [textInput, setTextInput] = useState('');
  const [showTextInput, setShowTextInput] = useState(false);
  const [textPosition, setTextPosition] = useState({ x: 0, y: 0 });
  
  // Collaboration
  const [cursors, setCursors] = useState({});
  const [isCollaborating, setIsCollaborating] = useState(true);

  useEffect(() => {
    // Set up real-time collaboration
    onWhiteboardEvent('draw', (data) => {
      if (data.userId !== user?._id) {
        setShapes(prevShapes => {
          const newShapes = [...prevShapes];
          const existingIndex = newShapes.findIndex(shape => shape.id === data.shape.id);
          
          if (existingIndex >= 0) {
            newShapes[existingIndex] = data.shape;
          } else {
            newShapes.push(data.shape);
          }
          
          return newShapes;
        });
      }
    });

    onWhiteboardEvent('clear', () => {
      setShapes([]);
      setHistory([[]]);
      setHistoryStep(0);
    });

    onWhiteboardEvent('cursorMove', (data) => {
      if (data.userId !== user?._id) {
        setCursors(prev => ({
          ...prev,
          [data.userId]: {
            x: data.x,
            y: data.y,
            user: data.user
          }
        }));
      }
    });

    onWhiteboardEvent('userLeft', (data) => {
      setCursors(prev => {
        const newCursors = { ...prev };
        delete newCursors[data.userId];
        return newCursors;
      });
    });

    return () => {
      offWhiteboardEvent('draw');
      offWhiteboardEvent('clear');
      offWhiteboardEvent('cursorMove');
      offWhiteboardEvent('userLeft');
    };
  }, [user, onWhiteboardEvent, offWhiteboardEvent]);

  const generateId = () => {
    return Date.now().toString() + Math.random().toString(36).substr(2, 9);
  };

  const addToHistory = (newShapes) => {
    const newHistory = history.slice(0, historyStep + 1);
    newHistory.push([...newShapes]);
    setHistory(newHistory);
    setHistoryStep(newHistory.length - 1);
  };

  const handleMouseDown = (e) => {
    if (tool === 'select') return;
    
    setIsDrawing(true);
    const pos = e.target.getStage().getPointerPosition();
    
    let newShape;
    
    switch (tool) {
      case 'pen':
        newShape = {
          id: generateId(),
          type: 'line',
          points: [pos.x, pos.y],
          stroke: strokeColor,
          strokeWidth: strokeWidth,
          globalCompositeOperation: 'source-over'
        };
        break;
        
      case 'eraser':
        newShape = {
          id: generateId(),
          type: 'line',
          points: [pos.x, pos.y],
          stroke: strokeColor,
          strokeWidth: strokeWidth * 2,
          globalCompositeOperation: 'destination-out'
        };
        break;
        
      case 'rectangle':
        newShape = {
          id: generateId(),
          type: 'rectangle',
          x: pos.x,
          y: pos.y,
          width: 0,
          height: 0,
          stroke: strokeColor,
          strokeWidth: strokeWidth,
          fill: fillColor
        };
        break;
        
      case 'circle':
        newShape = {
          id: generateId(),
          type: 'circle',
          x: pos.x,
          y: pos.y,
          radius: 0,
          stroke: strokeColor,
          strokeWidth: strokeWidth,
          fill: fillColor
        };
        break;
        
      case 'text':
        setTextPosition(pos);
        setShowTextInput(true);
        return;
        
      default:
        return;
    }
    
    const newShapes = [...shapes, newShape];
    setShapes(newShapes);
    
    if (isCollaborating) {
      emitWhiteboardEvent('draw', { shape: newShape, userId: user?._id });
    }
  };

  const handleMouseMove = (e) => {
    const stage = e.target.getStage();
    const point = stage.getPointerPosition();
    
    // Emit cursor position for collaboration
    if (isCollaborating && isConnected) {
      emitWhiteboardEvent('cursorMove', {
        x: point.x,
        y: point.y,
        userId: user?._id,
        user: { name: user?.name, color: '#667eea' }
      });
    }
    
    if (!isDrawing) return;
    
    const newShapes = [...shapes];
    const currentShape = newShapes[newShapes.length - 1];
    
    switch (currentShape.type) {
      case 'line':
        currentShape.points = currentShape.points.concat([point.x, point.y]);
        break;
        
      case 'rectangle':
        currentShape.width = point.x - currentShape.x;
        currentShape.height = point.y - currentShape.y;
        break;
        
      case 'circle':
        const radius = Math.sqrt(
          Math.pow(point.x - currentShape.x, 2) + Math.pow(point.y - currentShape.y, 2)
        );
        currentShape.radius = radius;
        break;
        
      default:
        break;
    }
    
    setShapes(newShapes);
    
    if (isCollaborating) {
      emitWhiteboardEvent('draw', { shape: currentShape, userId: user?._id });
    }
  };

  const handleMouseUp = () => {
    if (!isDrawing) return;
    
    setIsDrawing(false);
    addToHistory(shapes);
  };

  const handleTextSubmit = (e) => {
    e.preventDefault();
    if (!textInput.trim()) {
      setShowTextInput(false);
      return;
    }
    
    const newShape = {
      id: generateId(),
      type: 'text',
      x: textPosition.x,
      y: textPosition.y,
      text: textInput,
      fontSize: 16,
      fill: strokeColor,
      fontFamily: 'Arial'
    };
    
    const newShapes = [...shapes, newShape];
    setShapes(newShapes);
    addToHistory(newShapes);
    
    if (isCollaborating) {
      emitWhiteboardEvent('draw', { shape: newShape, userId: user?._id });
    }
    
    setTextInput('');
    setShowTextInput(false);
  };

  const clearCanvas = () => {
    setShapes([]);
    setHistory([[]]);
    setHistoryStep(0);
    
    if (isCollaborating) {
      emitWhiteboardEvent('clear', { userId: user?._id });
    }
  };

  const undo = () => {
    if (historyStep > 0) {
      setHistoryStep(historyStep - 1);
      setShapes(history[historyStep - 1]);
    }
  };

  const redo = () => {
    if (historyStep < history.length - 1) {
      setHistoryStep(historyStep + 1);
      setShapes(history[historyStep + 1]);
    }
  };

  const handleShapeClick = (shapeId) => {
    if (tool === 'select') {
      setSelectedShapeId(shapeId);
    }
  };

  const deleteSelected = () => {
    if (selectedShapeId) {
      const newShapes = shapes.filter(shape => shape.id !== selectedShapeId);
      setShapes(newShapes);
      addToHistory(newShapes);
      setSelectedShapeId(null);
      
      if (isCollaborating) {
        emitWhiteboardEvent('delete', { shapeId: selectedShapeId, userId: user?._id });
      }
    }
  };

  const downloadCanvas = () => {
    const uri = stageRef.current.toDataURL();
    const link = document.createElement('a');
    link.download = 'whiteboard.png';
    link.href = uri;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const renderShape = (shape) => {
    const isSelected = shape.id === selectedShapeId;
    const commonProps = {
      key: shape.id,
      onClick: () => handleShapeClick(shape.id),
      draggable: tool === 'select',
      stroke: isSelected ? '#667eea' : shape.stroke,
      strokeWidth: isSelected ? shape.strokeWidth + 1 : shape.strokeWidth
    };
    
    switch (shape.type) {
      case 'line':
        return (
          <Line
            {...commonProps}
            points={shape.points}
            globalCompositeOperation={shape.globalCompositeOperation}
            lineCap="round"
            lineJoin="round"
          />
        );
        
      case 'rectangle':
        return (
          <Rect
            {...commonProps}
            x={shape.x}
            y={shape.y}
            width={shape.width}
            height={shape.height}
            fill={shape.fill}
          />
        );
        
      case 'circle':
        return (
          <Circle
            {...commonProps}
            x={shape.x}
            y={shape.y}
            radius={shape.radius}
            fill={shape.fill}
          />
        );
        
      case 'text':
        return (
          <Text
            {...commonProps}
            x={shape.x}
            y={shape.y}
            text={shape.text}
            fontSize={shape.fontSize}
            fill={shape.fill}
            fontFamily={shape.fontFamily}
          />
        );
        
      default:
        return null;
    }
  };

  return (
    <div className="whiteboard-container">
      <div className="whiteboard-header">
        <div className="header-content">
          <h1>Collaborative Whiteboard</h1>
          <p>Draw, sketch, and brainstorm together in real-time</p>
        </div>
        
        <div className="collaboration-status">
          <div className={`connection-indicator ${isConnected ? 'connected' : 'disconnected'}`}>
            <span className="status-dot"></span>
            {isConnected ? 'Connected' : 'Disconnected'}
          </div>
          
          {activeUsers.length > 0 && (
            <div className="active-users">
              <span>{activeUsers.length} users online</span>
            </div>
          )}
        </div>
      </div>

      <div className="whiteboard-toolbar">
        <div className="tool-group">
          <button 
            className={`tool-btn ${tool === 'select' ? 'active' : ''}`}
            onClick={() => setTool('select')}
            title="Select"
          >
            ↖️
          </button>
          <button 
            className={`tool-btn ${tool === 'pen' ? 'active' : ''}`}
            onClick={() => setTool('pen')}
            title="Pen"
          >
            ✏️
          </button>
          <button 
            className={`tool-btn ${tool === 'eraser' ? 'active' : ''}`}
            onClick={() => setTool('eraser')}
            title="Eraser"
          >
            🧹
          </button>
          <button 
            className={`tool-btn ${tool === 'rectangle' ? 'active' : ''}`}
            onClick={() => setTool('rectangle')}
            title="Rectangle"
          >
            ⬜
          </button>
          <button 
            className={`tool-btn ${tool === 'circle' ? 'active' : ''}`}
            onClick={() => setTool('circle')}
            title="Circle"
          >
            ⭕
          </button>
          <button 
            className={`tool-btn ${tool === 'text' ? 'active' : ''}`}
            onClick={() => setTool('text')}
            title="Text"
          >
            📝
          </button>
        </div>

        <div className="tool-group">
          <div className="color-picker">
            <label>Color:</label>
            <input
              type="color"
              value={strokeColor}
              onChange={(e) => setStrokeColor(e.target.value)}
            />
          </div>
          
          <div className="stroke-width">
            <label>Size:</label>
            <input
              type="range"
              min="1"
              max="20"
              value={strokeWidth}
              onChange={(e) => setStrokeWidth(parseInt(e.target.value))}
            />
            <span>{strokeWidth}px</span>
          </div>
          
          {(tool === 'rectangle' || tool === 'circle') && (
            <div className="fill-picker">
              <label>Fill:</label>
              <input
                type="color"
                value={fillColor === 'transparent' ? '#ffffff' : fillColor}
                onChange={(e) => setFillColor(e.target.value)}
              />
              <button
                className={`transparent-btn ${fillColor === 'transparent' ? 'active' : ''}`}
                onClick={() => setFillColor('transparent')}
                title="No fill"
              >
                🚫
              </button>
            </div>
          )}
        </div>

        <div className="tool-group">
          <button 
            className="tool-btn"
            onClick={undo}
            disabled={historyStep <= 0}
            title="Undo"
          >
            ↶
          </button>
          <button 
            className="tool-btn"
            onClick={redo}
            disabled={historyStep >= history.length - 1}
            title="Redo"
          >
            ↷
          </button>
          <button 
            className="tool-btn"
            onClick={deleteSelected}
            disabled={!selectedShapeId}
            title="Delete Selected"
          >
            🗑️
          </button>
          <button 
            className="tool-btn clear-btn"
            onClick={clearCanvas}
            title="Clear All"
          >
            🧽
          </button>
        </div>

        <div className="tool-group">
          <button 
            className="tool-btn"
            onClick={downloadCanvas}
            title="Download"
          >
            💾
          </button>
          <button 
            className={`tool-btn ${isCollaborating ? 'active' : ''}`}
            onClick={() => setIsCollaborating(!isCollaborating)}
            title="Toggle Collaboration"
          >
            👥
          </button>
        </div>
      </div>

      <div className="whiteboard-canvas">
        <Stage
          ref={stageRef}
          width={window.innerWidth - 40}
          height={window.innerHeight - 200}
          onMouseDown={handleMouseDown}
          onMousemove={handleMouseMove}
          onMouseup={handleMouseUp}
          onTouchStart={handleMouseDown}
          onTouchMove={handleMouseMove}
          onTouchEnd={handleMouseUp}
        >
          <Layer>
            {shapes.map(shape => renderShape(shape))}
            
            {/* Render other users' cursors */}
            {Object.entries(cursors).map(([userId, cursor]) => (
              <React.Fragment key={userId}>
                <Circle
                  x={cursor.x}
                  y={cursor.y}
                  radius={5}
                  fill={cursor.user.color || '#667eea'}
                  opacity={0.7}
                />
                <Text
                  x={cursor.x + 10}
                  y={cursor.y - 5}
                  text={cursor.user.name}
                  fontSize={12}
                  fill={cursor.user.color || '#667eea'}
                  fontStyle="bold"
                />
              </React.Fragment>
            ))}
            
            {/* Selection transformer */}
            {selectedShapeId && (
              <Transformer
                nodes={[stageRef.current?.findOne(`#${selectedShapeId}`)].filter(Boolean)}
                boundBoxFunc={(oldBox, newBox) => {
                  if (newBox.width < 5 || newBox.height < 5) {
                    return oldBox;
                  }
                  return newBox;
                }}
              />
            )}
          </Layer>
        </Stage>

        {/* Text Input Modal */}
        {showTextInput && (
          <div className="text-input-modal">
            <form onSubmit={handleTextSubmit}>
              <input
                type="text"
                value={textInput}
                onChange={(e) => setTextInput(e.target.value)}
                placeholder="Enter text..."
                autoFocus
              />
              <div className="text-input-actions">
                <button type="submit">Add</button>
                <button type="button" onClick={() => setShowTextInput(false)}>
                  Cancel
                </button>
              </div>
            </form>
          </div>
        )}
      </div>

      {/* Active Users Panel */}
      {activeUsers.length > 0 && (
        <div className="users-panel">
          <h4>Active Users</h4>
          <div className="users-list">
            {activeUsers.map(user => (
              <div key={user.userId} className="user-item">
                <div className="user-avatar">
                  {user.name?.charAt(0).toUpperCase()}
                </div>
                <span>{user.name}</span>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default Whiteboard;
