import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useSocket } from '../contexts/SocketContext';
import axios from 'axios';
import './Projects.css';

const Projects = () => {
  const { user } = useAuth();
  const { isConnected } = useSocket();
  const navigate = useNavigate();
  const menuRef = useRef(null);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterStatus, setFilterStatus] = useState('all');
  const [sortBy, setSortBy] = useState('recent');
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [activeMenu, setActiveMenu] = useState(null);
  const [newProject, setNewProject] = useState({
    name: '',
    description: '',
    status: 'planning',
    priority: 'medium',
    dueDate: '',
    isPublic: true
  });

  // Load projects on component mount
  useEffect(() => {
    loadProjects();
  }, []);

  // Close menu when clicking outside or pressing Escape
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (menuRef.current && !menuRef.current.contains(event.target)) {
        setActiveMenu(null);
      }
    };

    const handleKeyDown = (event) => {
      if (event.key === 'Escape') {
        setActiveMenu(null);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    document.addEventListener('keydown', handleKeyDown);
    
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, []);

  const loadProjects = async () => {
    try {
      setLoading(true);
      const response = await axios.get('/api/projects');
      // Handle the API response structure: { success: true, data: { projects: [...] } }
      const projectsData = response.data?.data?.projects || [];
      setProjects(Array.isArray(projectsData) ? projectsData : []);
    } catch (error) {
      console.error('Error loading projects:', error);
      // Mock data for development
      setProjects([
        {
          _id: '1',
          name: 'Website Redesign',
          description: 'Complete overhaul of company website with modern design',
          status: 'active',
          priority: 'high',
          owner: { name: 'Sarah Johnson', _id: 'user1' },
          members: [
            { name: 'Sarah Johnson', _id: 'user1', role: 'Project Manager' },
            { name: 'Mike Chen', _id: 'user2', role: 'Designer' },
            { name: 'Alex Rivera', _id: 'user3', role: 'Developer' },
            { name: 'Emma Davis', _id: 'user4', role: 'QA Tester' }
          ],
          progress: 75,
          tasks: { total: 24, completed: 18, inProgress: 4, todo: 2 },
          dueDate: '2025-09-15',
          createdAt: '2025-07-01',
          updatedAt: '2025-08-20',
          isPublic: true
        },
        {
          _id: '2',
          name: 'Mobile App Development',
          description: 'Native mobile application for iOS and Android platforms',
          status: 'active',
          priority: 'high',
          owner: { name: 'John Smith', _id: 'user5' },
          members: [
            { name: 'John Smith', _id: 'user5', role: 'Project Manager' },
            { name: 'Lisa Wong', _id: 'user6', role: 'Mobile Developer' },
            { name: 'David Brown', _id: 'user7', role: 'UI/UX Designer' }
          ],
          progress: 45,
          tasks: { total: 32, completed: 14, inProgress: 8, todo: 10 },
          dueDate: '2025-10-30',
          createdAt: '2025-06-15',
          updatedAt: '2025-08-21',
          isPublic: false
        },
        {
          _id: '3',
          name: 'Brand Identity Refresh',
          description: 'Updated brand guidelines and visual identity system',
          status: 'review',
          priority: 'medium',
          owner: { name: 'Emma Davis', _id: 'user4' },
          members: [
            { name: 'Emma Davis', _id: 'user4', role: 'Creative Director' },
            { name: 'Mike Chen', _id: 'user2', role: 'Graphic Designer' }
          ],
          progress: 90,
          tasks: { total: 12, completed: 11, inProgress: 1, todo: 0 },
          dueDate: '2025-08-25',
          createdAt: '2025-07-20',
          updatedAt: '2025-08-19',
          isPublic: true
        },
        {
          _id: '4',
          name: 'Market Research Study',
          description: 'Comprehensive analysis of target market and competitors',
          status: 'completed',
          priority: 'low',
          owner: { name: 'Alex Rivera', _id: 'user3' },
          members: [
            { name: 'Alex Rivera', _id: 'user3', role: 'Research Lead' },
            { name: 'Sarah Johnson', _id: 'user1', role: 'Analyst' }
          ],
          progress: 100,
          tasks: { total: 8, completed: 8, inProgress: 0, todo: 0 },
          dueDate: '2025-07-31',
          createdAt: '2025-06-01',
          updatedAt: '2025-07-31',
          isPublic: true
        }
      ]);
    } finally {
      setLoading(false);
    }
  };

  const handleCreateProject = async (e) => {
    e.preventDefault();
    
    // Frontend validation to match backend requirements
    if (newProject.name.trim().length < 3) {
      alert('Project name must be at least 3 characters long');
      return;
    }
    
    if (newProject.description.trim().length < 10) {
      alert('Project description must be at least 10 characters long');
      return;
    }
    
    try {
      // Transform frontend data to match backend API
      const projectData = {
        title: newProject.name.trim(),
        description: newProject.description.trim(),
        priority: newProject.priority,
        endDate: newProject.dueDate || null,
        settings: {
          isPublic: newProject.isPublic
        }
      };

      const response = await axios.post('/api/projects', projectData);
      const currentProjects = Array.isArray(projects) ? projects : [];
      
      // Handle the API response structure: { success: true, data: { project: {...} } }
      const newProjectData = response.data?.data?.project || response.data;
      setProjects([newProjectData, ...currentProjects]);
      
      setShowCreateModal(false);
      setNewProject({
        name: '',
        description: '',
        status: 'planning',
        priority: 'medium',
        dueDate: '',
        isPublic: true
      });
    } catch (error) {
      console.error('Error creating project:', error);
      // Show user-friendly error message
      if (error.response?.data?.message) {
        alert(`Error: ${error.response.data.message}`);
      } else {
        alert('Failed to create project. Please try again.');
      }
    }
  };

  const handleProjectClick = (projectId) => {
    navigate(`/projects/${projectId}`);
  };

  const handleViewKanban = (projectId, e) => {
    e.stopPropagation(); // Prevent card click
    setActiveMenu(null); // Close menu
    navigate(`/kanban/${projectId}`);
  };

  const handleDeleteProject = async (projectId, projectName, e) => {
    e.stopPropagation(); // Prevent card click
    
    if (window.confirm(`Are you sure you want to delete "${projectName}"? This action cannot be undone.`)) {
      try {
        await axios.delete(`/api/projects/${projectId}`);
        setProjects(projects.filter(p => p._id !== projectId));
        setActiveMenu(null);
        alert('Project deleted successfully');
      } catch (error) {
        console.error('Error deleting project:', error);
        alert('Failed to delete project. Please try again.');
      }
    }
  };

  const handleSaveProject = async (projectId, e) => {
    e.stopPropagation(); // Prevent card click
    
    // For now, just show a success message
    // In a real app, this might save to favorites, bookmarks, etc.
    const projectName = projects.find(p => p._id === projectId)?.name || 
                       projects.find(p => p._id === projectId)?.title || 'Project';
    alert(`"${projectName}" saved to your favorites! 💾`);
    setActiveMenu(null);
  };

  const handleEditProject = (projectId, e) => {
    e.stopPropagation(); // Prevent card click
    setActiveMenu(null);
    navigate(`/projects/${projectId}/edit`);
  };

  const toggleMenu = (projectId, e) => {
    e.stopPropagation(); // Prevent card click
    setActiveMenu(activeMenu === projectId ? null : projectId);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'active': return '#38a169';
      case 'planning': return '#3182ce';
      case 'review': return '#d69e2e';
      case 'completed': return '#805ad5';
      case 'on-hold': return '#e53e3e';
      default: return '#718096';
    }
  };

  const getPriorityColor = (priority) => {
    switch (priority) {
      case 'high': return '#e53e3e';
      case 'medium': return '#d69e2e';
      case 'low': return '#38a169';
      default: return '#718096';
    }
  };

  const filteredAndSortedProjects = (Array.isArray(projects) ? projects : [])
    .filter(project => {
      // Handle both 'name' and 'title' fields, and ensure they exist
      const projectName = project.name || project.title || '';
      const projectDescription = project.description || '';
      
      const matchesSearch = projectName.toLowerCase().includes(searchTerm.toLowerCase()) ||
                          projectDescription.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesStatus = filterStatus === 'all' || project.status === filterStatus;
      return matchesSearch && matchesStatus;
    })
    .sort((a, b) => {
      switch (sortBy) {
        case 'name':
          const aName = a.name || a.title || '';
          const bName = b.name || b.title || '';
          return aName.localeCompare(bName);
        case 'dueDate':
          const aDueDate = a.dueDate || a.endDate || '';
          const bDueDate = b.dueDate || b.endDate || '';
          return new Date(aDueDate) - new Date(bDueDate);
        case 'progress':
          return (b.progress || 0) - (a.progress || 0);
        case 'recent':
        default:
          const aUpdated = a.updatedAt || a.createdAt || '';
          const bUpdated = b.updatedAt || b.createdAt || '';
          return new Date(bUpdated) - new Date(aUpdated);
      }
    });

  if (loading) {
    return (
      <div className="projects-loading">
        <div className="loading-spinner large"></div>
        <p>Loading projects...</p>
      </div>
    );
  }

  return (
    <div className="projects-page">
      <div className="projects-header">
        <div className="header-content">
          <h1>Projects</h1>
          <p>Manage and collaborate on your team's projects</p>
        </div>
        
        <div className="header-actions">
          <button 
            className="create-project-btn"
            onClick={() => setShowCreateModal(true)}
          >
            <span className="btn-icon">➕</span>
            New Project
          </button>
        </div>
      </div>

      {/* Filters and Search */}
      <div className="projects-controls">
        <div className="search-box">
          <input
            type="text"
            placeholder="Search projects..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
          <span className="search-icon">🔍</span>
        </div>

        <div className="filters">
          <select
            value={filterStatus}
            onChange={(e) => setFilterStatus(e.target.value)}
            className="filter-select"
          >
            <option value="all">All Status</option>
            <option value="planning">Planning</option>
            <option value="active">Active</option>
            <option value="review">Review</option>
            <option value="completed">Completed</option>
            <option value="on-hold">On Hold</option>
          </select>

          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="filter-select"
          >
            <option value="recent">Recently Updated</option>
            <option value="name">Name</option>
            <option value="dueDate">Due Date</option>
            <option value="progress">Progress</option>
          </select>
        </div>
      </div>

      {/* Projects Grid */}
      <div className="projects-grid">
        {filteredAndSortedProjects.map(project => (
          <div
            key={project._id}
            className="project-card"
            onClick={() => handleProjectClick(project._id)}
          >
            <div className="project-header">
              <div className="project-title">
                <h3>{project.name || project.title}</h3>
                <div className="project-badges">
                  <span 
                    className="status-badge"
                    style={{ backgroundColor: getStatusColor(project.status) }}
                  >
                    {project.status}
                  </span>
                  <span 
                    className="priority-badge"
                    style={{ color: getPriorityColor(project.priority) }}
                  >
                    {project.priority}
                  </span>
                </div>
              </div>
              <div className="project-menu" ref={activeMenu === project._id ? menuRef : null}>
                <button 
                  className="menu-btn"
                  onClick={(e) => toggleMenu(project._id, e)}
                >
                  ⋮
                </button>
                {activeMenu === project._id && (
                  <div className="menu-dropdown">
                    <button 
                      className="menu-item kanban-btn"
                      onClick={(e) => handleViewKanban(project._id, e)}
                    >
                      <span>📋</span>
                      View Kanban Board
                    </button>
                    <button 
                      className="menu-item save-btn"
                      onClick={(e) => handleSaveProject(project._id, e)}
                    >
                      <span>💾</span>
                      Save to Favorites
                    </button>
                    <button 
                      className="menu-item edit-btn"
                      onClick={(e) => handleEditProject(project._id, e)}
                    >
                      <span>✏️</span>
                      Edit Project
                    </button>
                    <button 
                      className="menu-item delete-btn"
                      onClick={(e) => handleDeleteProject(project._id, project.name || project.title, e)}
                    >
                      <span>🗑️</span>
                      Delete Project
                    </button>
                  </div>
                )}
              </div>
            </div>

            <p className="project-description">{project.description || 'No description available'}</p>

            <div className="project-progress">
              <div className="progress-header">
                <span>Progress</span>
                <span>{project.progress || 0}%</span>
              </div>
              <div className="progress-bar">
                <div 
                  className="progress-fill"
                  style={{ width: `${project.progress || 0}%` }}
                ></div>
              </div>
            </div>

            <div className="project-stats">
              <div className="stat">
                <span className="stat-value">{project.tasks?.total || 0}</span>
                <span className="stat-label">Total Tasks</span>
              </div>
              <div className="stat">
                <span className="stat-value">{project.tasks?.completed || 0}</span>
                <span className="stat-label">Completed</span>
              </div>
              <div className="stat">
                <span className="stat-value">{project.members?.length || 0}</span>
                <span className="stat-label">Members</span>
              </div>
            </div>

            <div className="project-footer">
              <div className="project-members">
                {(project.members || []).slice(0, 3).map((member, index) => (
                  <div key={member._id} className="member-avatar" title={member.name}>
                    {member.name.charAt(0).toUpperCase()}
                  </div>
                ))}
                {(project.members?.length || 0) > 3 && (
                  <div className="member-avatar more">
                    +{(project.members?.length || 0) - 3}
                  </div>
                )}
              </div>

              <div className="project-due-date">
                <span className="due-label">Due:</span>
                <span className="due-date">
                  {project.dueDate || project.endDate ? 
                    new Date(project.dueDate || project.endDate).toLocaleDateString() : 
                    'No due date'
                  }
                </span>
              </div>
            </div>
          </div>
        ))}
      </div>

      {filteredAndSortedProjects.length === 0 && (
        <div className="no-projects">
          <div className="no-projects-icon">📋</div>
          <h3>No Projects Found</h3>
          <p>Try adjusting your search or filter criteria</p>
        </div>
      )}

      {/* Create Project Modal */}
      {showCreateModal && (
        <div className="modal-overlay" onClick={() => setShowCreateModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h2>Create New Project</h2>
              <button 
                className="modal-close"
                onClick={() => setShowCreateModal(false)}
              >
                ✕
              </button>
            </div>

            <form onSubmit={handleCreateProject} className="create-project-form">
              <div className="form-group">
                <label htmlFor="projectName">Project Name</label>
                <input
                  type="text"
                  id="projectName"
                  value={newProject.name}
                  onChange={(e) => setNewProject({...newProject, name: e.target.value})}
                  required
                  minLength={3}
                  maxLength={100}
                  placeholder="Enter project name (min 3 characters)"
                />
              </div>

              <div className="form-group">
                <label htmlFor="projectDescription">Description</label>
                <textarea
                  id="projectDescription"
                  value={newProject.description}
                  onChange={(e) => setNewProject({...newProject, description: e.target.value})}
                  placeholder="Describe your project (min 10 characters)"
                  required
                  minLength={10}
                  maxLength={1000}
                  rows="3"
                ></textarea>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label htmlFor="projectStatus">Status</label>
                  <select
                    id="projectStatus"
                    value={newProject.status}
                    onChange={(e) => setNewProject({...newProject, status: e.target.value})}
                  >
                    <option value="planning">Planning</option>
                    <option value="active">Active</option>
                    <option value="review">Review</option>
                  </select>
                </div>

                <div className="form-group">
                  <label htmlFor="projectPriority">Priority</label>
                  <select
                    id="projectPriority"
                    value={newProject.priority}
                    onChange={(e) => setNewProject({...newProject, priority: e.target.value})}
                  >
                    <option value="low">Low</option>
                    <option value="medium">Medium</option>
                    <option value="high">High</option>
                  </select>
                </div>
              </div>

              <div className="form-group">
                <label htmlFor="projectDueDate">Due Date</label>
                <input
                  type="date"
                  id="projectDueDate"
                  value={newProject.dueDate}
                  onChange={(e) => setNewProject({...newProject, dueDate: e.target.value})}
                />
              </div>

              <div className="form-group">
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    checked={newProject.isPublic}
                    onChange={(e) => setNewProject({...newProject, isPublic: e.target.checked})}
                  />
                  <span className="checkmark"></span>
                  Make project public
                </label>
              </div>

              <div className="modal-actions">
                <button 
                  type="button" 
                  className="btn-secondary"
                  onClick={() => setShowCreateModal(false)}
                >
                  Cancel
                </button>
                <button type="submit" className="btn-primary">
                  Create Project
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Projects;
