import React from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

const ProtectedRoute = ({ 
  children, 
  requiredRole = null, 
  requireProjectAccess = false, 
  projectId = null 
}) => {
  const { isAuthenticated, user, loading } = useAuth();
  const location = useLocation();

  // Show loading state while checking authentication
  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-spinner"></div>
        <p>Loading...</p>
      </div>
    );
  }

  // Redirect to login if not authenticated
  if (!isAuthenticated) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  // Check role-based access if required
  if (requiredRole && user?.role !== requiredRole) {
    // Admin has access to all roles
    if (user?.role !== 'admin') {
      return (
        <div className="access-denied">
          <h2>Access Denied</h2>
          <p>You don't have permission to access this page.</p>
          <p>Required role: {requiredRole}</p>
          <p>Your role: {user?.role}</p>
        </div>
      );
    }
  }

  // Check project-specific access if required
  if (requireProjectAccess && projectId && user) {
    // This would typically check if user has access to the specific project
    // For now, we'll assume basic role checking is sufficient
    const hasProjectAccess = user.role === 'admin' || 
                             user.role === 'project_manager' || 
                             user.role === 'team_member';
    
    if (!hasProjectAccess) {
      return (
        <div className="access-denied">
          <h2>Project Access Required</h2>
          <p>You need to be a team member to access this project.</p>
        </div>
      );
    }
  }

  return children;
};

export default ProtectedRoute;
