import React, { useState, useEffect, useCallback } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { useParams } from 'react-router-dom';
import { useSocket } from '../contexts/SocketContext';
import axios from 'axios';
import './KanbanBoard.css';

const KanbanBoard = () => {
  const { projectId } = useParams();
  const { socket } = useSocket();
  
  const [columns, setColumns] = useState({
    'todo': {
      id: 'todo',
      title: 'To Do',
      color: '#e2e8f0',
      tasks: []
    },
    'in_progress': {
      id: 'in_progress',
      title: 'In Progress',
      color: '#fed7b7',
      tasks: []
    },
    'review': {
      id: 'review',
      title: 'Review',
      color: '#fef2a7',
      tasks: []
    },
    'done': {
      id: 'done',
      title: 'Done',
      color: '#c6f6d5',
      tasks: []
    }
  });
  
  const [loading, setLoading] = useState(true);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [editingTask, setEditingTask] = useState(null);
  const [selectedColumn, setSelectedColumn] = useState('todo');
  const [newTask, setNewTask] = useState({
    title: '',
    description: '',
    priority: 'medium',
    assignedTo: '',
    dueDate: '',
    tags: []
  });

  // Mock team members for assignment
  const teamMembers = [
    { _id: 'user1', name: 'Sarah Johnson', email: 'sarah@example.com' },
    { _id: 'user2', name: 'Mike Chen', email: 'mike@example.com' },
    { _id: 'user3', name: 'Alex Rivera', email: 'alex@example.com' },
    { _id: 'user4', name: 'Emma Davis', email: 'emma@example.com' }
  ];

  // Function declarations moved before useEffect
  const organizeTasks = useCallback((tasks) => {
    const newColumns = {
      'todo': {
        id: 'todo',
        title: 'To Do',
        color: '#e2e8f0',
        tasks: []
      },
      'in_progress': {
        id: 'in_progress',
        title: 'In Progress',
        color: '#fed7b7',
        tasks: []
      },
      'review': {
        id: 'review',
        title: 'Review',
        color: '#fef2a7',
        tasks: []
      },
      'done': {
        id: 'done',
        title: 'Done',
        color: '#c6f6d5',
        tasks: []
      }
    };
    
    // Organize tasks by status
    tasks.forEach(task => {
      const columnId = task.status;
      if (newColumns[columnId]) {
        newColumns[columnId].tasks.push(task);
      }
    });
    
    setColumns(newColumns);
  }, []);

  const loadTasks = useCallback(async () => {
    try {
      setLoading(true);
      
      // Handle demo mode
      if (projectId === 'demo') {
        const mockTasks = [
          {
            _id: 'task1',
            title: 'Design wireframes for homepage',
            description: 'Create detailed wireframes for the new homepage layout including mobile and desktop versions',
            status: 'todo',
            priority: 'high',
            assignedTo: [{ _id: 'user2', name: 'Mike Chen' }],
            dueDate: '2025-08-25',
            tags: ['design', 'frontend'],
            createdAt: '2025-08-20',
            comments: 3,
            attachments: 1
          },
          {
            _id: 'task2',
            title: 'Implement user authentication',
            description: 'Set up JWT-based authentication system with login, register, and password reset functionality',
            status: 'in_progress',
            priority: 'high',
            assignedTo: [{ _id: 'user1', name: 'Sarah Johnson' }],
            dueDate: '2025-08-28',
            tags: ['backend', 'security'],
            createdAt: '2025-08-19',
            comments: 5,
            attachments: 2
          },
          {
            _id: 'task3',
            title: 'Write API documentation',
            description: 'Document all REST API endpoints with examples and response formats',
            status: 'done',
            priority: 'medium',
            assignedTo: [{ _id: 'user3', name: 'Alex Rivera' }],
            dueDate: '2025-08-22',
            tags: ['documentation', 'api'],
            createdAt: '2025-08-18',
            comments: 2,
            attachments: 0
          },
          {
            _id: 'task4',
            title: 'Setup CI/CD pipeline',
            description: 'Configure automated testing and deployment pipeline using GitHub Actions',
            status: 'review',
            priority: 'low',
            assignedTo: [{ _id: 'user4', name: 'Emma Davis' }],
            dueDate: '2025-09-05',
            tags: ['devops', 'automation'],
            createdAt: '2025-08-21',
            comments: 0,
            attachments: 0
          }
        ];
        organizeTasks(mockTasks);
        setLoading(false);
        return;
      }
      
      const response = await axios.get(`/api/projects/${projectId}/tasks`);
      const tasks = response.data;
      organizeTasks(tasks);
    } catch (error) {
      console.error('Error loading tasks:', error);
      // Mock data for development
      const mockTasks = [
        {
          _id: 'task1',
          title: 'Design wireframes for homepage',
          description: 'Create detailed wireframes for the new homepage layout including mobile and desktop versions',
          status: 'todo',
          priority: 'high',
          assignedTo: [{ _id: 'user2', name: 'Mike Chen' }],
          dueDate: '2025-08-25',
          tags: ['design', 'frontend'],
          createdAt: '2025-08-20',
          comments: 3,
          attachments: 1
        },
        {
          _id: 'task2',
          title: 'Implement user authentication',
          description: 'Set up JWT-based authentication system with login, register, and password reset functionality',
          status: 'in_progress',
          priority: 'high',
          assignedTo: [{ _id: 'user1', name: 'Sarah Johnson' }],
          dueDate: '2025-08-28',
          tags: ['backend', 'security'],
          createdAt: '2025-08-19',
          comments: 5,
          attachments: 2
        },
        {
          _id: 'task3',
          title: 'Write API documentation',
          description: 'Document all REST API endpoints with examples and response formats',
          status: 'done',
          priority: 'medium',
          assignedTo: [{ _id: 'user3', name: 'Alex Rivera' }],
          dueDate: '2025-08-22',
          tags: ['documentation', 'api'],
          createdAt: '2025-08-18',
          comments: 2,
          attachments: 0
        },
        {
          _id: 'task4',
          title: 'Setup CI/CD pipeline',
          description: 'Configure automated testing and deployment pipeline using GitHub Actions',
          status: 'review',
          priority: 'low',
          assignedTo: [{ _id: 'user4', name: 'Emma Davis' }],
          dueDate: '2025-09-05',
          tags: ['documentation'],
          createdAt: '2025-08-21',
          comments: 0,
          attachments: 0
        }
      ];
      organizeTasks(mockTasks);
    } finally {
      setLoading(false);
    }
  }, [projectId, organizeTasks]);

  const updateTaskInColumns = useCallback((updatedTask) => {
    setColumns(prevColumns => {
      const newColumns = { ...prevColumns };
      
      // Find and update the task in all columns
      Object.keys(newColumns).forEach(columnId => {
        const taskIndex = newColumns[columnId].tasks.findIndex(task => task._id === updatedTask._id);
        if (taskIndex !== -1) {
          // If task status changed, move it to new column
          if (updatedTask.status !== columnId) {
            newColumns[columnId].tasks.splice(taskIndex, 1);
            if (newColumns[updatedTask.status]) {
              newColumns[updatedTask.status].tasks.push(updatedTask);
            }
          } else {
            // Update task in same column
            newColumns[columnId].tasks[taskIndex] = updatedTask;
          }
        }
      });
      
      return newColumns;
    });
  }, []);

  const moveTaskBetweenColumns = useCallback((taskId, fromColumn, toColumn, newIndex) => {
    setColumns(prevColumns => {
      const newColumns = { ...prevColumns };
      
      // Find the task in the source column
      const taskIndex = newColumns[fromColumn].tasks.findIndex(task => task._id === taskId);
      if (taskIndex !== -1) {
        const [movedTask] = newColumns[fromColumn].tasks.splice(taskIndex, 1);
        movedTask.status = toColumn;
        newColumns[toColumn].tasks.splice(newIndex, 0, movedTask);
      }
      
      return newColumns;
    });
  }, []);

  // Socket event handlers and initial load
  useEffect(() => {
    loadTasks();
  }, [loadTasks]);

  const handleDragEnd = async (result) => {
    const { destination, source, draggableId } = result;

    if (!destination) return;

    if (destination.droppableId === source.droppableId && destination.index === source.index) {
      return;
    }

    // Update local state immediately for responsiveness
    moveTaskBetweenColumns(draggableId, source.droppableId, destination.droppableId, destination.index);

    // Update backend
    try {
      await axios.put(`/api/tasks/${draggableId}`, {
        status: destination.droppableId
      });
    } catch (error) {
      console.error('Error updating task status:', error);
      // Revert local state on error
      moveTaskBetweenColumns(draggableId, destination.droppableId, source.droppableId, source.index);
    }
  };

  const createTask = async (e) => {
    e.preventDefault();
    
    // Validate required fields
    if (!newTask.title.trim()) {
      alert('Task title is required (minimum 3 characters)');
      return;
    }

    if (newTask.title.trim().length < 3) {
      alert('Task title must be at least 3 characters long');
      return;
    }

    if (!projectId) {
      alert('Project ID is missing. Please refresh the page and try again.');
      return;
    }

    if (projectId === 'demo') {
      alert('Task creation is disabled in demo mode. Create a real project to add tasks.');
      return;
    }

    console.log('Creating task with data:', newTask);
    console.log('Project ID:', projectId);

    try {
      const taskData = {
        title: newTask.title.trim(),
        description: newTask.description.trim(),
        project: projectId, // Server expects 'project', not 'projectId'
        status: selectedColumn, // Set the status based on selected column
        assignedTo: newTask.assignedTo ? [newTask.assignedTo] : [], // Server expects array
        priority: newTask.priority,
        dueDate: newTask.dueDate || null,
        tags: newTask.tags.filter(tag => tag.trim() !== '')
      };

      console.log('Sending task data to server:', taskData);

      const response = await axios.post('/api/tasks', taskData);

      console.log('Server response:', response.data);

      const task = response.data.data.task; // Server returns { success: true, data: { task } }
      
      // Add task to the appropriate column
      setColumns(prevColumns => {
        const newColumns = { ...prevColumns };
        const targetColumn = selectedColumn || 'todo';
        if (newColumns[targetColumn]) {
          newColumns[targetColumn].tasks.push(task);
        }
        return newColumns;
      });
      
      // Emit socket event for real-time updates
      if (socket) {
        socket.emit('taskCreated', { 
          task, 
          projectId: projectId
        });
      }

      // Reset form
      setNewTask({
        title: '',
        description: '',
        priority: 'medium',
        assignedTo: '',
        dueDate: '',
        tags: []
      });
      setShowCreateModal(false);
      alert('Task created successfully!');
    } catch (error) {
      console.error('Error creating task:', error);
      console.error('Error response:', error.response?.data);
      
      // More detailed error handling
      if (error.response?.data?.errors && Array.isArray(error.response.data.errors)) {
        const errorMessages = error.response.data.errors.map(err => err.msg).join('\n');
        alert(`Validation failed:\n${errorMessages}`);
      } else if (error.response?.data?.message) {
        alert(`Failed to create task: ${error.response.data.message}`);
      } else {
        alert(`Failed to create task: ${error.message}`);
      }
    }
  };

  const editTask = (task) => {
    setEditingTask(task);
    setNewTask({
      title: task.title,
      description: task.description || '',
      priority: task.priority,
      assignedTo: task.assignedTo && task.assignedTo.length > 0 
        ? (typeof task.assignedTo[0] === 'object' ? task.assignedTo[0]._id : task.assignedTo[0])
        : '',
      dueDate: task.dueDate ? new Date(task.dueDate).toISOString().split('T')[0] : '',
      tags: task.tags || []
    });
    setShowEditModal(true);
  };

  const updateTask = async (e) => {
    e.preventDefault();
    
    if (!newTask.title.trim()) {
      alert('Task title is required');
      return;
    }

    try {
      const taskData = {
        title: newTask.title.trim(),
        description: newTask.description.trim(),
        status: editingTask?.status || 'todo',
        priority: newTask.priority,
        assignedTo: newTask.assignedTo ? [newTask.assignedTo] : [],
        dueDate: newTask.dueDate || null,
        tags: newTask.tags.filter(tag => tag.trim() !== '')
      };

      const response = await axios.put(`/api/tasks/${editingTask._id}`, taskData);
      const updatedTask = response.data.data.task;

      // Update task in columns
      updateTaskInColumns(updatedTask);

      if (socket) {
        socket.emit('taskUpdated', { 
          task: updatedTask, 
          projectId: projectId
        });
      }

      setNewTask({
        title: '',
        description: '',
        priority: 'medium',
        assignedTo: '',
        dueDate: '',
        tags: []
      });
      setShowEditModal(false);
      setEditingTask(null);
      alert('Task updated successfully!');
    } catch (error) {
      console.error('Error updating task:', error);
      alert(`Failed to update task: ${error.response?.data?.message || error.message}`);
    }
  };

  const deleteTask = async (taskId) => {
    if (!window.confirm('Are you sure you want to delete this task?')) {
      return;
    }

    try {
      await axios.delete(`/api/tasks/${taskId}`);
      
      // Remove task from columns
      setColumns(prevColumns => {
        const newColumns = { ...prevColumns };
        Object.keys(newColumns).forEach(columnId => {
          newColumns[columnId].tasks = newColumns[columnId].tasks.filter(task => task._id !== taskId);
        });
        return newColumns;
      });

      if (socket) {
        socket.emit('taskDeleted', { 
          taskId, 
          projectId: projectId
        });
      }

      alert('Task deleted successfully!');
    } catch (error) {
      console.error('Error deleting task:', error);
      alert(`Failed to delete task: ${error.response?.data?.message || error.message}`);
    }
  };  const getPriorityColor = (priority) => {
    switch (priority) {
      case 'low': return '#10b981';
      case 'medium': return '#f59e0b';
      case 'high': return '#ef4444';
      default: return '#6b7280';
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      year: 'numeric'
    });
  };

  if (loading) {
    return (
      <div className="kanban-loading">
        <div className="loading-spinner"></div>
        <p>Loading tasks...</p>
      </div>
    );
  }

  // Check if projectId is missing
  if (!projectId) {
    return (
      <div className="kanban-container">
        <div className="kanban-header">
          <h1>Project Tasks</h1>
        </div>
        <div className="error-message" style={{
          textAlign: 'center',
          padding: '2rem',
          backgroundColor: '#fee2e2',
          border: '1px solid #fecaca',
          borderRadius: '8px',
          margin: '2rem'
        }}>
          <h3>Project ID Required</h3>
          <p>Please access the Kanban board from a specific project.</p>
          <p>Go to <strong>Projects</strong> → Select a project → Click the <strong>📋 View Kanban Board</strong> option.</p>
          <div style={{ marginTop: '1rem', display: 'flex', gap: '1rem', justifyContent: 'center' }}>
            <button 
              className="btn btn-primary"
              onClick={() => window.location.href = '/projects'}
            >
              Go to Projects
            </button>
            <button 
              className="btn btn-secondary"
              onClick={() => window.location.href = '/kanban/demo'}
              style={{ backgroundColor: '#6b7280', color: 'white' }}
            >
              View Demo Board
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="kanban-container">
      <div className="kanban-header">
        <h1>
          Project Tasks
          {projectId === 'demo' && (
            <span style={{ 
              fontSize: '0.7em', 
              color: '#6b7280', 
              fontWeight: 'normal',
              marginLeft: '1rem'
            }}>
              (Demo Mode)
            </span>
          )}
        </h1>
        <button 
          className="btn btn-primary"
          onClick={() => setShowCreateModal(true)}
          disabled={projectId === 'demo'}
          title={projectId === 'demo' ? 'Task creation disabled in demo mode' : 'Add new task'}
        >
          <span className="icon">+</span>
          Add Task
        </button>
      </div>

      <DragDropContext onDragEnd={handleDragEnd}>
        <div className="kanban-board">
          {Object.values(columns).map(column => (
            <div key={column.id} className="kanban-column">
              <div className="column-header">
                <h3>{column.title}</h3>
                <span className="task-count">{column.tasks.length}</span>
              </div>
              
              <Droppable droppableId={column.id}>
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    {...provided.droppableProps}
                    className={`column-content ${snapshot.isDraggingOver ? 'dragging-over' : ''}`}
                  >
                    {column.tasks.map((task, index) => (
                      <Draggable key={task._id} draggableId={task._id} index={index}>
                        {(provided, snapshot) => (
                          <div
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                            className={`task-card ${snapshot.isDragging ? 'dragging' : ''}`}
                          >
                            <div className="task-header">
                              <h4>{task.title}</h4>
                              <div className="task-actions">
                                <button 
                                  className="action-btn edit-btn"
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    editTask(task);
                                  }}
                                  title="Edit task"
                                >
                                  ✏️
                                </button>
                                <button 
                                  className="action-btn delete-btn"
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    deleteTask(task._id);
                                  }}
                                  title="Delete task"
                                >
                                  🗑️
                                </button>
                              </div>
                              <div 
                                className="priority-indicator"
                                style={{ backgroundColor: getPriorityColor(task.priority) }}
                              >
                                {task.priority}
                              </div>
                            </div>
                            
                            <p className="task-description">{task.description}</p>
                            
                            <div className="task-meta">
                              {task.assignedTo && task.assignedTo.length > 0 && (
                                <div className="assignee">
                                  <span className="assignee-avatar">
                                    {typeof task.assignedTo[0] === 'object' 
                                      ? task.assignedTo[0].name?.charAt(0) 
                                      : 'U'}
                                  </span>
                                  <span className="assignee-name">
                                    {typeof task.assignedTo[0] === 'object' 
                                      ? task.assignedTo[0].name 
                                      : 'Unassigned'}
                                  </span>
                                </div>
                              )}
                              
                              {task.dueDate && (
                                <div className="due-date">
                                  📅 {formatDate(task.dueDate)}
                                </div>
                              )}
                            </div>
                            
                            {task.tags && task.tags.length > 0 && (
                              <div className="task-tags">
                                {task.tags.map(tag => (
                                  <span key={tag} className="tag">{tag}</span>
                                ))}
                              </div>
                            )}
                            
                            <div className="task-stats">
                              <span className="stat">💬 {task.comments || 0}</span>
                              <span className="stat">📎 {task.attachments || 0}</span>
                            </div>
                          </div>
                        )}
                      </Draggable>
                    ))}
                    {column.tasks.length === 0 && (
                      <div className="empty-column">
                        <span className="empty-message">
                          {column.id === 'todo' ? 'No tasks yet. Create one!' : `No ${column.title.toLowerCase()} tasks`}
                        </span>
                      </div>
                    )}
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          ))}
        </div>
      </DragDropContext>

      {/* Create Task Modal */}
      {showCreateModal && (
        <div className="modal-overlay" onClick={() => setShowCreateModal(false)}>
          <div className="modal-content" onClick={e => e.stopPropagation()}>
            <div className="modal-header">
              <h2>Create New Task</h2>
              <button 
                className="close-btn"
                onClick={() => setShowCreateModal(false)}
              >
                ×
              </button>
            </div>

            <form onSubmit={createTask} className="task-form">
              <div className="form-group">
                <label>Title *</label>
                <input
                  type="text"
                  value={newTask.title}
                  onChange={(e) => setNewTask({...newTask, title: e.target.value})}
                  placeholder="Enter task title (minimum 3 characters)..."
                  required
                  minLength={3}
                />
              </div>

              <div className="form-group">
                <label>Description</label>
                <textarea
                  value={newTask.description}
                  onChange={(e) => setNewTask({...newTask, description: e.target.value})}
                  placeholder="Enter task description (optional)..."
                  rows="3"
                />
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>Column</label>
                  <select
                    value={selectedColumn}
                    onChange={(e) => setSelectedColumn(e.target.value)}
                  >
                    {Object.values(columns).map(column => (
                      <option key={column.id} value={column.id}>
                        {column.title}
                      </option>
                    ))}
                  </select>
                </div>

                <div className="form-group">
                  <label>Priority</label>
                  <select
                    value={newTask.priority}
                    onChange={(e) => setNewTask({...newTask, priority: e.target.value})}
                  >
                    <option value="low">Low</option>
                    <option value="medium">Medium</option>
                    <option value="high">High</option>
                  </select>
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>Assign To</label>
                  <select
                    value={newTask.assignedTo}
                    onChange={(e) => setNewTask({...newTask, assignedTo: e.target.value})}
                  >
                    <option value="">Unassigned</option>
                    {teamMembers.map(member => (
                      <option key={member._id} value={member._id}>
                        {member.name}
                      </option>
                    ))}
                  </select>
                </div>

                <div className="form-group">
                  <label>Due Date</label>
                  <input
                    type="date"
                    value={newTask.dueDate}
                    onChange={(e) => setNewTask({...newTask, dueDate: e.target.value})}
                  />
                </div>
              </div>

              <div className="form-group">
                <label>Tags (comma separated)</label>
                <input
                  type="text"
                  value={newTask.tags.join(', ')}
                  onChange={(e) => setNewTask({
                    ...newTask, 
                    tags: e.target.value.split(',').map(tag => tag.trim())
                  })}
                  placeholder="frontend, design, urgent"
                />
              </div>

              <div className="form-actions">
                <button type="button" onClick={() => setShowCreateModal(false)}>
                  Cancel
                </button>
                <button type="submit" className="btn-primary">
                  Create Task
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Edit Task Modal */}
      {showEditModal && (
        <div className="modal-overlay" onClick={() => setShowEditModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3>Edit Task</h3>
              <button 
                className="close-btn"
                onClick={() => setShowEditModal(false)}
              >
                ×
              </button>
            </div>
            
            <form onSubmit={updateTask} className="task-form">
              <div className="form-group">
                <label>Task Title</label>
                <input
                  type="text"
                  value={newTask.title}
                  onChange={(e) => setNewTask({...newTask, title: e.target.value})}
                  placeholder="Enter task title..."
                  required
                />
              </div>

              <div className="form-group">
                <label>Description</label>
                <textarea
                  value={newTask.description}
                  onChange={(e) => setNewTask({...newTask, description: e.target.value})}
                  placeholder="Enter task description..."
                  rows="3"
                />
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>Status</label>
                  <select
                    value={editingTask?.status || 'todo'}
                    onChange={(e) => setEditingTask({...editingTask, status: e.target.value})}
                  >
                    <option value="todo">To Do</option>
                    <option value="in_progress">In Progress</option>
                    <option value="review">Review</option>
                    <option value="done">Done</option>
                  </select>
                </div>

                <div className="form-group">
                  <label>Priority</label>
                  <select
                    value={newTask.priority}
                    onChange={(e) => setNewTask({...newTask, priority: e.target.value})}
                  >
                    <option value="low">Low</option>
                    <option value="medium">Medium</option>
                    <option value="high">High</option>
                  </select>
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>Assign To</label>
                  <select
                    value={newTask.assignedTo}
                    onChange={(e) => setNewTask({...newTask, assignedTo: e.target.value})}
                  >
                    <option value="">Unassigned</option>
                    {teamMembers.map(member => (
                      <option key={member._id} value={member._id}>
                        {member.name}
                      </option>
                    ))}
                  </select>
                </div>

                <div className="form-group">
                  <label>Due Date</label>
                  <input
                    type="date"
                    value={newTask.dueDate}
                    onChange={(e) => setNewTask({...newTask, dueDate: e.target.value})}
                  />
                </div>
              </div>

              <div className="form-group">
                <label>Tags (comma separated)</label>
                <input
                  type="text"
                  value={newTask.tags.join(', ')}
                  onChange={(e) => setNewTask({
                    ...newTask, 
                    tags: e.target.value.split(',').map(tag => tag.trim())
                  })}
                  placeholder="frontend, design, urgent"
                />
              </div>

              <div className="form-actions">
                <button type="button" onClick={() => setShowEditModal(false)}>
                  Cancel
                </button>
                <button type="submit" className="btn-primary">
                  Update Task
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default KanbanBoard;
