import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useSocket } from '../contexts/SocketContext';
import './DocumentEditor.css';

const DocumentEditor = () => {
  const { user } = useAuth();
  const { socket } = useSocket();
  const [documents, setDocuments] = useState([]);
  const [currentDocument, setCurrentDocument] = useState(null);
  const [content, setContent] = useState('');
  const [title, setTitle] = useState('');
  const [isEditing, setIsEditing] = useState(false);
  const [collaborators, setCollaborators] = useState([]);
  const [comments, setComments] = useState([]);
  const [showComments, setShowComments] = useState(false);
  const [newComment, setNewComment] = useState('');
  const [selectedText, setSelectedText] = useState('');
  const [documentHistory, setDocumentHistory] = useState([]); // Re-enabled since it's used
  const [currentVersion, setCurrentVersion] = useState(0);
  const [saveStatus, setSaveStatus] = useState('saved');
  const [showNewDocModal, setShowNewDocModal] = useState(false);
  const [newDocTitle, setNewDocTitle] = useState('');
  const [newDocTemplate, setNewDocTemplate] = useState('blank');
  const [searchTerm, setSearchTerm] = useState('');
  const editorRef = useRef(null);
  const saveTimeoutRef = useRef(null);

  // Mock document data
  const mockDocuments = [
    {
      id: 'doc-1',
      title: 'Project Requirements',
      content: '<h1>Project Requirements Document</h1><p>This document outlines the requirements for our new project...</p>',
      lastModified: new Date('2024-01-15'),
      author: 'John Doe',
      collaborators: ['Alice Smith', 'Bob Johnson'],
      template: 'requirements'
    },
    {
      id: 'doc-2',
      title: 'Meeting Notes - Jan 14',
      content: '<h2>Weekly Team Meeting</h2><p><strong>Date:</strong> January 14, 2024</p><ul><li>Discussed project timeline</li><li>Reviewed design mockups</li></ul>',
      lastModified: new Date('2024-01-14'),
      author: 'Alice Smith',
      collaborators: ['John Doe'],
      template: 'meeting'
    },
    {
      id: 'doc-3',
      title: 'API Documentation',
      content: '<h1>API Documentation</h1><h2>Authentication</h2><p>All API requests require authentication...</p>',
      lastModified: new Date('2024-01-13'),
      author: 'Bob Johnson',
      collaborators: ['John Doe', 'Alice Smith'],
      template: 'documentation'
    }
  ];

  // Mock comments data
  const mockComments = [
    {
      id: 'comment-1',
      documentId: 'doc-1',
      author: 'Alice Smith',
      content: 'This section needs more detail about the user authentication flow.',
      selectedText: 'user authentication',
      timestamp: new Date('2024-01-15T10:30:00'),
      resolved: false,
      position: { start: 150, end: 170 }
    },
    {
      id: 'comment-2',
      documentId: 'doc-1',
      author: 'Bob Johnson',
      content: 'Should we include mobile-specific requirements here?',
      selectedText: 'requirements',
      timestamp: new Date('2024-01-15T11:15:00'),
      resolved: false,
      position: { start: 50, end: 62 }
    }
  ];

  // Enhanced document templates
  const documentTemplates = {
    blank: {
      title: 'Blank Document',
      content: '<h1>Document Title</h1><p>Start writing your document here...</p>'
    },
    meeting: {
      title: 'Meeting Notes',
      content: `
        <h1>Meeting Notes</h1>
        
        <p><strong>Date:</strong> ${new Date().toLocaleDateString()}</p>
        <p><strong>Meeting Type:</strong> Weekly Team Meeting</p>
        <p><strong>Attendees:</strong> [Add attendee names]</p>
        <p><strong>Duration:</strong> [Meeting duration]</p>
        
        <h2>Agenda</h2>
        <ol>
          <li>Review previous action items</li>
          <li>Project status updates</li>
          <li>New business items</li>
          <li>Next steps and action items</li>
        </ol>
        
        <h2>Discussion Points</h2>
        <p>Key discussion points and decisions made during the meeting...</p>
        
        <h2>Action Items</h2>
        <table border="1" style="border-collapse: collapse; width: 100%;">
          <tr style="background-color: #f8f9fa;">
            <th style="padding: 8px;">Task</th>
            <th style="padding: 8px;">Assigned To</th>
            <th style="padding: 8px;">Due Date</th>
            <th style="padding: 8px;">Status</th>
          </tr>
          <tr>
            <td style="padding: 8px;">Sample action item</td>
            <td style="padding: 8px;">[Name]</td>
            <td style="padding: 8px;">[Date]</td>
            <td style="padding: 8px;">Pending</td>
          </tr>
        </table>
        
        <h2>Next Meeting</h2>
        <p><strong>Date:</strong> [Next meeting date]</p>
        <p><strong>Agenda Preview:</strong> [Items for next meeting]</p>
      `
    },
    requirements: {
      title: 'Requirements Document',
      content: `
        <h1>Project Requirements Document</h1>
        
        <h2>1. Project Overview</h2>
        <h3>1.1 Purpose</h3>
        <p>This document outlines the functional and non-functional requirements for [Project Name].</p>
        
        <h3>1.2 Scope</h3>
        <p>Define what is included and excluded from this project...</p>
        
        <h3>1.3 Definitions and Acronyms</h3>
        <ul>
          <li><strong>API:</strong> Application Programming Interface</li>
          <li><strong>UI:</strong> User Interface</li>
          <li><strong>UX:</strong> User Experience</li>
        </ul>
        
        <h2>2. Functional Requirements</h2>
        <h3>2.1 User Management</h3>
        <table border="1" style="border-collapse: collapse; width: 100%;">
          <tr style="background-color: #f8f9fa;">
            <th style="padding: 8px;">ID</th>
            <th style="padding: 8px;">Requirement</th>
            <th style="padding: 8px;">Priority</th>
          </tr>
          <tr>
            <td style="padding: 8px;">FR-001</td>
            <td style="padding: 8px;">Users must be able to register with email and password</td>
            <td style="padding: 8px;">High</td>
          </tr>
          <tr>
            <td style="padding: 8px;">FR-002</td>
            <td style="padding: 8px;">Users must be able to login securely</td>
            <td style="padding: 8px;">High</td>
          </tr>
        </table>
        
        <h2>3. Non-Functional Requirements</h2>
        <h3>3.1 Performance</h3>
        <ul>
          <li>System response time must be under 2 seconds</li>
          <li>System must support 1000 concurrent users</li>
        </ul>
        
        <h3>3.2 Security</h3>
        <ul>
          <li>All passwords must be encrypted</li>
          <li>HTTPS must be used for all communications</li>
        </ul>
        
        <h2>4. Acceptance Criteria</h2>
        <p>Define the criteria that must be met for each requirement to be considered complete...</p>
      `
    },
    documentation: {
      title: 'Technical Documentation',
      content: `
        <h1>Technical Documentation</h1>
        
        <h2>Table of Contents</h2>
        <ol>
          <li><a href="#getting-started">Getting Started</a></li>
          <li><a href="#installation">Installation</a></li>
          <li><a href="#configuration">Configuration</a></li>
          <li><a href="#api-reference">API Reference</a></li>
          <li><a href="#troubleshooting">Troubleshooting</a></li>
        </ol>
        
        <h2 id="getting-started">Getting Started</h2>
        <p>Welcome to the system documentation. This guide will help you understand and use the system effectively.</p>
        
        <h3>Prerequisites</h3>
        <ul>
          <li>Node.js version 16 or higher</li>
          <li>npm or yarn package manager</li>
          <li>Database (MongoDB/PostgreSQL)</li>
        </ul>
        
        <h2 id="installation">Installation</h2>
        <h3>Clone the Repository</h3>
        <pre><code>git clone https://github.com/yourproject/repo.git
cd repo</code></pre>
        
        <h3>Install Dependencies</h3>
        <pre><code>npm install</code></pre>
        
        <h3>Start the Application</h3>
        <pre><code>npm start</code></pre>
        
        <h2 id="configuration">Configuration</h2>
        <p>Configure the system by creating a <code>.env</code> file with the following variables:</p>
        <pre><code>DATABASE_URL=your_database_url
JWT_SECRET=your_jwt_secret
PORT=3000</code></pre>
        
        <h2 id="api-reference">API Reference</h2>
        <h3>Authentication</h3>
        <p>All API requests require authentication via Bearer token in the Authorization header.</p>
        
        <h3>Endpoints</h3>
        <table border="1" style="border-collapse: collapse; width: 100%;">
          <tr style="background-color: #f8f9fa;">
            <th style="padding: 8px;">Method</th>
            <th style="padding: 8px;">Endpoint</th>
            <th style="padding: 8px;">Description</th>
          </tr>
          <tr>
            <td style="padding: 8px;">GET</td>
            <td style="padding: 8px;">/api/users</td>
            <td style="padding: 8px;">Retrieve all users</td>
          </tr>
          <tr>
            <td style="padding: 8px;">POST</td>
            <td style="padding: 8px;">/api/users</td>
            <td style="padding: 8px;">Create a new user</td>
          </tr>
        </table>
        
        <h2 id="troubleshooting">Troubleshooting</h2>
        <h3>Common Issues</h3>
        <p><strong>Issue:</strong> Application won't start</p>
        <p><strong>Solution:</strong> Check that all environment variables are set correctly and dependencies are installed.</p>
      `
    },
    proposal: {
      title: 'Project Proposal',
      content: `
        <h1>Project Proposal: [Project Name]</h1>
        
        <h2>Executive Summary</h2>
        <p>Provide a brief overview of the proposed project, its objectives, and expected outcomes...</p>
        
        <h2>Problem Statement</h2>
        <p>Clearly describe the problem or opportunity this project addresses...</p>
        
        <h2>Proposed Solution</h2>
        <p>Detail the proposed solution and how it will address the identified problem...</p>
        
        <h2>Project Objectives</h2>
        <ul>
          <li>Primary objective 1</li>
          <li>Primary objective 2</li>
          <li>Secondary objective 1</li>
        </ul>
        
        <h2>Timeline and Milestones</h2>
        <table border="1" style="border-collapse: collapse; width: 100%;">
          <tr style="background-color: #f8f9fa;">
            <th style="padding: 8px;">Phase</th>
            <th style="padding: 8px;">Duration</th>
            <th style="padding: 8px;">Key Deliverables</th>
            <th style="padding: 8px;">Milestone</th>
          </tr>
          <tr>
            <td style="padding: 8px;">Phase 1: Planning</td>
            <td style="padding: 8px;">2 weeks</td>
            <td style="padding: 8px;">Project plan, requirements document</td>
            <td style="padding: 8px;">Planning complete</td>
          </tr>
          <tr>
            <td style="padding: 8px;">Phase 2: Development</td>
            <td style="padding: 8px;">8 weeks</td>
            <td style="padding: 8px;">Working prototype, initial testing</td>
            <td style="padding: 8px;">MVP ready</td>
          </tr>
          <tr>
            <td style="padding: 8px;">Phase 3: Testing</td>
            <td style="padding: 8px;">2 weeks</td>
            <td style="padding: 8px;">Test results, bug fixes</td>
            <td style="padding: 8px;">Quality assured</td>
          </tr>
        </table>
        
        <h2>Budget Estimate</h2>
        <table border="1" style="border-collapse: collapse; width: 100%;">
          <tr style="background-color: #f8f9fa;">
            <th style="padding: 8px;">Category</th>
            <th style="padding: 8px;">Cost</th>
            <th style="padding: 8px;">Notes</th>
          </tr>
          <tr>
            <td style="padding: 8px;">Personnel</td>
            <td style="padding: 8px;">$XX,XXX</td>
            <td style="padding: 8px;">Development team costs</td>
          </tr>
          <tr>
            <td style="padding: 8px;">Infrastructure</td>
            <td style="padding: 8px;">$X,XXX</td>
            <td style="padding: 8px;">Servers, tools, licenses</td>
          </tr>
        </table>
        
        <h2>Risk Assessment</h2>
        <ul>
          <li><strong>Technical Risk:</strong> Description and mitigation strategy</li>
          <li><strong>Timeline Risk:</strong> Description and mitigation strategy</li>
          <li><strong>Resource Risk:</strong> Description and mitigation strategy</li>
        </ul>
        
        <h2>Success Criteria</h2>
        <p>Define measurable criteria that will indicate project success...</p>
      `
    }
  };

  // Socket event handlers - moved before useEffect
  const handleDocumentUpdate = useCallback((data) => {
    if (currentDocument && data.documentId === currentDocument.id) {
      setContent(data.content);
      setSaveStatus('saved');
    }
  }, [currentDocument]);

  const handleUserJoined = useCallback((data) => {
    setCollaborators(prev => [...prev.filter(c => c.id !== data.user.id), data.user]);
  }, []);

  const handleUserLeft = useCallback((data) => {
    setCollaborators(prev => prev.filter(c => c.id !== data.user.id));
  }, []);

  const handleCommentAdded = useCallback((comment) => {
    setComments(prev => [...prev, comment]);
  }, []);

  const handleCursorPosition = useCallback((data) => {
    // Handle real-time cursor positions from other users
    console.log('Cursor position update:', data);
  }, []);

  useEffect(() => {
    // Initialize with mock data
    setDocuments(mockDocuments);
    setComments(mockComments);

    // Set up socket listeners for real-time collaboration
    if (socket) {
      socket.on('document-updated', handleDocumentUpdate);
      socket.on('user-joined-document', handleUserJoined);
      socket.on('user-left-document', handleUserLeft);
      socket.on('comment-added', handleCommentAdded);
      socket.on('cursor-position', handleCursorPosition);

      return () => {
        socket.off('document-updated');
        socket.off('user-joined-document');
        socket.off('user-left-document');
        socket.off('comment-added');
        socket.off('cursor-position');
      };
    }
  }, [socket, handleDocumentUpdate, handleUserJoined, handleUserLeft, handleCommentAdded, handleCursorPosition, mockComments, mockDocuments]);

  // Initialize editor content when document changes
  useEffect(() => {
    if (editorRef.current && currentDocument) {
      // Only set content if editor is empty or document changed
      if (editorRef.current.innerHTML !== content && !isEditing) {
        editorRef.current.innerHTML = content || '';
      }
    }
  }, [currentDocument]); // Removed content dependency to prevent loops

  // Handle keyboard events for better text editing
  const handleKeyDown = (e) => {
    // Allow all normal typing characters and navigation keys
    if (
      e.key.length === 1 || // Regular characters
      e.key === 'Backspace' || 
      e.key === 'Delete' || 
      e.key === 'Enter' ||
      e.key === 'Tab' ||
      e.key.startsWith('Arrow') ||
      e.key === 'Home' ||
      e.key === 'End' ||
      e.key === 'PageUp' ||
      e.key === 'PageDown'
    ) {
      // Let browser handle these naturally - don't prevent default
      return;
    }

    // Handle special key combinations
    if (e.ctrlKey || e.metaKey) {
      switch (e.key.toLowerCase()) {
        case 'b':
          e.preventDefault();
          formatText('bold');
          break;
        case 'i':
          e.preventDefault();
          formatText('italic');
          break;
        case 'u':
          e.preventDefault();
          formatText('underline');
          break;
        case 's':
          e.preventDefault();
          saveDocument();
          break;
        case 'z':
          e.preventDefault();
          if (e.shiftKey) {
            formatText('redo');
          } else {
            formatText('undo');
          }
          break;
        case 'f':
          e.preventDefault();
          setShowFindReplace(!showFindReplace);
          break;
        case 'a':
          // Allow Ctrl+A (select all)
          return;
        case 'c':
        case 'v':
        case 'x':
          // Allow copy, paste, cut
          return;
        default:
          break;
      }
    }
  };

  // Handle paste events to clean up formatting
  const handlePaste = (e) => {
    e.preventDefault();
    
    // Get plain text from clipboard
    const paste = (e.clipboardData || window.clipboardData).getData('text/plain');
    
    // Insert as plain text to avoid formatting issues
    const selection = window.getSelection();
    if (!selection.rangeCount) return;
    
    const range = selection.getRangeAt(0);
    range.deleteContents();
    
    // Create text node and insert
    const textNode = document.createTextNode(paste);
    range.insertNode(textNode);
    
    // Move cursor to end of inserted text
    range.setStartAfter(textNode);
    range.setEndAfter(textNode);
    selection.removeAllRanges();
    selection.addRange(range);
    
    // Trigger content change
    if (editorRef.current) {
      const event = { target: { innerHTML: editorRef.current.innerHTML } };
      handleContentChange(event);
    }
  };

  const openDocument = (document) => {
    // Save current document if editing
    if (currentDocument && isEditing) {
      saveDocument();
    }

    setCurrentDocument(document);
    setContent(document.content);
    setTitle(document.title);
    setIsEditing(false);
    setSaveStatus('saved');
    
    // Update editor content immediately
    if (editorRef.current) {
      editorRef.current.innerHTML = document.content || '';
    }
    
    // Join document room for real-time collaboration
    if (socket) {
      socket.emit('join-document', { documentId: document.id, user });
    }

    // Load document history
    setDocumentHistory([
      { version: 1, content: document.content, timestamp: new Date(), author: document.author },
      { version: 2, content: document.content, timestamp: new Date(), author: user?.name || 'Current User' }
    ]);
    setCurrentVersion(2);
  };

  const createDocument = () => {
    const template = documentTemplates[newDocTemplate];
    const newDoc = {
      id: `doc-${Date.now()}`,
      title: newDocTitle || template.title,
      content: template.content,
      lastModified: new Date(),
      author: user?.name || 'Current User',
      collaborators: [],
      template: newDocTemplate
    };

    setDocuments(prev => [newDoc, ...prev]);
    openDocument(newDoc);
    setShowNewDocModal(false);
    setNewDocTitle('');
    setNewDocTemplate('blank');
  };

  const handleContentChange = (e) => {
    // Get the current content from the editor
    const newContent = e.target.innerHTML;
    
    // Only update if content actually changed to prevent infinite loops
    if (newContent !== content) {
      setContent(newContent);
      setIsEditing(true);
      setSaveStatus('editing');

      // Debounce save
      if (saveTimeoutRef.current) {
        clearTimeout(saveTimeoutRef.current);
      }
      
      saveTimeoutRef.current = setTimeout(() => {
        autoSave(newContent);
      }, 2000);

      // Emit real-time updates
      if (socket && currentDocument) {
        socket.emit('document-change', {
          documentId: currentDocument.id,
          content: newContent,
          user
        });
      }
    }
  };

  const autoSave = (contentToSave) => {
    setSaveStatus('saving');
    
    // Simulate API call
    setTimeout(() => {
      if (currentDocument) {
        setDocuments(prev => prev.map(doc => 
          doc.id === currentDocument.id 
            ? { ...doc, content: contentToSave, lastModified: new Date() }
            : doc
        ));
        setSaveStatus('saved');
      }
    }, 500);
  };

  const saveDocument = () => {
    if (!currentDocument) return;
    
    setSaveStatus('saving');
    
    // Simulate API call
    setTimeout(() => {
      setDocuments(prev => prev.map(doc => 
        doc.id === currentDocument.id 
          ? { ...doc, content, title, lastModified: new Date() }
          : doc
      ));
      setIsEditing(false);
      setSaveStatus('saved');
      
      // Add to history
      const newVersion = {
        version: currentVersion + 1,
        content,
        timestamp: new Date(),
        author: user?.name || 'Current User'
      };
      setDocumentHistory(prev => [...prev, newVersion]);
      setCurrentVersion(newVersion.version);
    }, 500);
  };

  const handleTextSelection = () => {
    const selection = window.getSelection();
    if (selection.toString().length > 0) {
      setSelectedText(selection.toString());
    }
  };

  const addComment = () => {
    if (!newComment.trim() || !selectedText) return;

    const comment = {
      id: `comment-${Date.now()}`,
      documentId: currentDocument.id,
      author: user?.name || 'Current User',
      content: newComment,
      selectedText,
      timestamp: new Date(),
      resolved: false,
      position: { start: 0, end: selectedText.length }
    };

    setComments(prev => [...prev, comment]);
    setNewComment('');
    setSelectedText('');

    // Emit to other users
    if (socket) {
      socket.emit('add-comment', comment);
    }
  };

  const resolveComment = (commentId) => {
    setComments(prev => prev.map(comment => 
      comment.id === commentId 
        ? { ...comment, resolved: true }
        : comment
    ));
  };

  const formatText = (command, value = null) => {
    // Focus the editor first
    if (editorRef.current) {
      editorRef.current.focus();
    }
    
    try {
      // Use execCommand for basic formatting
      if (document.queryCommandSupported && document.queryCommandSupported(command)) {
        document.execCommand(command, false, value);
        
        // Trigger content change after formatting
        if (editorRef.current) {
          const event = { target: { innerHTML: editorRef.current.innerHTML } };
          handleContentChange(event);
        }
      }
    } catch (error) {
      console.warn('Format command failed:', command, error);
    }
  };

  const insertElement = (elementType) => {
    const editor = editorRef.current;
    if (!editor) return;

    const selection = window.getSelection();
    if (selection.rangeCount === 0) return;
    
    const range = selection.getRangeAt(0);

    let element;
    switch (elementType) {
      case 'table':
        const rows = prompt('Number of rows:', '3') || '3';
        const cols = prompt('Number of columns:', '3') || '3';
        element = document.createElement('table');
        element.style.border = '1px solid #ddd';
        element.style.borderCollapse = 'collapse';
        element.style.width = '100%';
        element.style.marginTop = '10px';
        element.style.marginBottom = '10px';
        
        let tableHTML = '<thead><tr>';
        for (let i = 0; i < parseInt(cols); i++) {
          tableHTML += `<th style="border: 1px solid #ddd; padding: 8px; background-color: #f8f9fa;">Header ${i + 1}</th>`;
        }
        tableHTML += '</tr></thead><tbody>';
        
        for (let i = 0; i < parseInt(rows) - 1; i++) {
          tableHTML += '<tr>';
          for (let j = 0; j < parseInt(cols); j++) {
            tableHTML += `<td style="border: 1px solid #ddd; padding: 8px;">Cell ${i + 1}-${j + 1}</td>`;
          }
          tableHTML += '</tr>';
        }
        tableHTML += '</tbody>';
        element.innerHTML = tableHTML;
        break;
        
      case 'image':
        const imageUrl = prompt('Enter image URL:');
        if (imageUrl) {
          element = document.createElement('img');
          element.src = imageUrl;
          element.style.maxWidth = '100%';
          element.style.height = 'auto';
          element.style.display = 'block';
          element.style.margin = '10px 0';
          element.alt = 'Inserted image';
        }
        break;
        
      case 'link':
        const linkUrl = prompt('Enter URL:');
        const linkText = prompt('Enter link text:') || linkUrl;
        if (linkUrl && linkText) {
          element = document.createElement('a');
          element.href = linkUrl;
          element.textContent = linkText;
          element.target = '_blank';
          element.style.color = '#007bff';
          element.style.textDecoration = 'underline';
        }
        break;
        
      case 'hr':
        element = document.createElement('hr');
        element.style.border = 'none';
        element.style.borderTop = '1px solid #ccc';
        element.style.margin = '20px 0';
        break;
        
      default:
        return;
    }

    if (element) {
      range.deleteContents();
      range.insertNode(element);
      
      // Move cursor after the inserted element
      range.setStartAfter(element);
      range.setEndAfter(element);
      selection.removeAllRanges();
      selection.addRange(range);
      
      // Trigger content change
      handleContentChange({ target: { innerHTML: editor.innerHTML } });
    }
  };

  // Export functions
  const exportToPDF = () => {
    const printWindow = window.open('', '_blank');
    printWindow.document.write(`
      <html>
        <head>
          <title>${title}</title>
          <style>
            body { font-family: Arial, sans-serif; margin: 40px; line-height: 1.6; }
            h1, h2, h3 { color: #333; }
            table { border-collapse: collapse; width: 100%; margin: 10px 0; }
            th, td { border: 1px solid #ddd; padding: 8px; text-align: left; }
            th { background-color: #f8f9fa; }
            img { max-width: 100%; height: auto; }
            pre { background-color: #f8f9fa; padding: 10px; border-radius: 4px; }
          </style>
        </head>
        <body>
          <h1>${title}</h1>
          ${content}
        </body>
      </html>
    `);
    printWindow.document.close();
    printWindow.print();
  };

  const exportToWord = () => {
    const blob = new Blob([`
      <html xmlns:o='urn:schemas-microsoft-com:office:office' xmlns:w='urn:schemas-microsoft-com:office:word' xmlns='http://www.w3.org/TR/REC-html40'>
        <head>
          <meta charset='utf-8'>
          <title>${title}</title>
        </head>
        <body>
          <h1>${title}</h1>
          ${content}
        </body>
      </html>
    `], { type: 'application/msword' });
    
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `${title.replace(/[^a-z0-9]/gi, '_').toLowerCase()}.doc`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const printDocument = () => {
    const printWindow = window.open('', '_blank');
    printWindow.document.write(`
      <html>
        <head>
          <title>${title}</title>
          <style>
            @media print {
              body { font-family: Arial, sans-serif; margin: 0; padding: 20px; line-height: 1.6; }
              h1, h2, h3 { color: #000; page-break-after: avoid; }
              table { border-collapse: collapse; width: 100%; page-break-inside: avoid; }
              th, td { border: 1px solid #000; padding: 8px; }
              th { background-color: #f0f0f0; }
              img { max-width: 100%; height: auto; page-break-inside: avoid; }
              pre { background-color: #f8f9fa; padding: 10px; border: 1px solid #ccc; }
            }
          </style>
        </head>
        <body>
          <h1>${title}</h1>
          ${content}
        </body>
      </html>
    `);
    printWindow.document.close();
    printWindow.print();
  };

  // Advanced editor functions
  const [showSpellCheck, setShowSpellCheck] = useState(true);
  const [showFindReplace, setShowFindReplace] = useState(false);
  const [findText, setFindText] = useState('');
  const [replaceText, setReplaceText] = useState('');

  const toggleSpellCheck = () => {
    const editor = editorRef.current;
    if (editor) {
      const currentSpellCheck = editor.getAttribute('spellcheck') === 'true';
      editor.setAttribute('spellcheck', !currentSpellCheck);
      setShowSpellCheck(!currentSpellCheck);
    }
  };

  const findAndHighlight = () => {
    if (!findText) return;
    
    const editor = editorRef.current;
    if (!editor) return;

    // Clear previous highlights
    const content = editor.innerHTML.replace(/<mark class="highlight">(.*?)<\/mark>/g, '$1');
    editor.innerHTML = content;

    // Add new highlights
    const regex = new RegExp(`(${findText.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')})`, 'gi');
    const highlightedContent = editor.innerHTML.replace(regex, '<mark class="highlight">$1</mark>');
    editor.innerHTML = highlightedContent;
  };

  const replaceAll = () => {
    if (!findText || !replaceText) return;
    
    const editor = editorRef.current;
    if (!editor) return;

    const regex = new RegExp(findText.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'), 'gi');
    const newContent = editor.innerHTML.replace(regex, replaceText);
    editor.innerHTML = newContent;
    
    handleContentChange({ target: { innerHTML: newContent } });
  };

  // Document statistics
  const getWordCount = () => {
    const text = editorRef.current?.textContent || '';
    return text.trim().split(/\s+/).filter(word => word.length > 0).length;
  };

  const getCharCount = () => {
    return editorRef.current?.textContent?.length || 0;
  };

  // Fix the incomplete insertElement function
  const completeElementInsertion = (element, range, selection, editor) => {
    if (element) {
      range.deleteContents();
      range.insertNode(element);
      
      // Move cursor after the inserted element
      range.setStartAfter(element);
      range.setEndAfter(element);
      selection.removeAllRanges();
      selection.addRange(range);
      
      // Trigger content change
      handleContentChange({ target: { innerHTML: editor.innerHTML } });
    }
  };

  const filteredDocuments = documents.filter(doc =>
    doc.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    doc.author.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const documentComments = comments.filter(comment => 
    currentDocument && comment.documentId === currentDocument.id && !comment.resolved
  );

  return (
    <div className="document-editor">
      {/* Sidebar */}
      <div className="documents-sidebar">
        <div className="sidebar-header">
          <h2>Documents</h2>
          <button 
            className="new-doc-btn"
            onClick={() => setShowNewDocModal(true)}
          >
            + New
          </button>
        </div>

        <div className="search-documents">
          <input
            type="text"
            placeholder="Search documents..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
        </div>

        <div className="documents-list">
          {filteredDocuments.map(doc => (
            <div 
              key={doc.id}
              className={`document-item ${currentDocument?.id === doc.id ? 'active' : ''}`}
              onClick={() => openDocument(doc)}
            >
              <div className="doc-title">{doc.title}</div>
              <div className="doc-meta">
                <span className="doc-author">{doc.author}</span>
                <span className="doc-date">
                  {doc.lastModified.toLocaleDateString()}
                </span>
              </div>
              <div className="doc-collaborators">
                {doc.collaborators.length > 0 && (
                  <span className="collaborator-count">
                    👥 {doc.collaborators.length}
                  </span>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Main Editor */}
      <div className="editor-main">
        {currentDocument ? (
          <>
            {/* Editor Header */}
            <div className="editor-header">
              <div className="doc-title-section">
                <input
                  type="text"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  className="title-input"
                  placeholder="Document title..."
                />
                <div className="save-status">
                  <span className={`status-indicator ${saveStatus}`}>
                    {saveStatus === 'saved' && '✓ Saved'}
                    {saveStatus === 'saving' && '⋯ Saving'}
                    {saveStatus === 'editing' && '• Editing'}
                  </span>
                </div>
              </div>

              <div className="editor-actions">
                <div className="collaborators-list">
                  {collaborators.map(collaborator => (
                    <div key={collaborator.id} className="collaborator-avatar" title={collaborator.name}>
                      {collaborator.name.charAt(0)}
                    </div>
                  ))}
                </div>
                
                <button 
                  className="comments-toggle"
                  onClick={() => setShowComments(!showComments)}
                >
                  💬 {documentComments.length}
                </button>
                
                <button 
                  className="save-btn"
                  onClick={saveDocument}
                  disabled={!isEditing}
                >
                  Save
                </button>
              </div>
            </div>

            {/* Enhanced Toolbar */}
            <div className="editor-toolbar">
              <div className="format-group">
                <button onClick={() => formatText('bold')} className="format-btn" title="Bold">
                  <strong>B</strong>
                </button>
                <button onClick={() => formatText('italic')} className="format-btn" title="Italic">
                  <em>I</em>
                </button>
                <button onClick={() => formatText('underline')} className="format-btn" title="Underline">
                  <u>U</u>
                </button>
                <button onClick={() => formatText('strikeThrough')} className="format-btn" title="Strikethrough">
                  <s>S</s>
                </button>
              </div>

              <div className="format-group">
                <button onClick={() => formatText('formatBlock', 'h1')} className="format-btn" title="Heading 1">H1</button>
                <button onClick={() => formatText('formatBlock', 'h2')} className="format-btn" title="Heading 2">H2</button>
                <button onClick={() => formatText('formatBlock', 'h3')} className="format-btn" title="Heading 3">H3</button>
                <button onClick={() => formatText('formatBlock', 'p')} className="format-btn" title="Paragraph">P</button>
              </div>

              <div className="format-group">
                <button onClick={() => formatText('insertUnorderedList')} className="format-btn" title="Bullet List">
                  • List
                </button>
                <button onClick={() => formatText('insertOrderedList')} className="format-btn" title="Numbered List">
                  1. List
                </button>
                <button onClick={() => formatText('indent')} className="format-btn" title="Increase Indent">
                  →
                </button>
                <button onClick={() => formatText('outdent')} className="format-btn" title="Decrease Indent">
                  ←
                </button>
              </div>

              <div className="format-group">
                <button onClick={() => formatText('justifyLeft')} className="format-btn" title="Align Left">
                  ⬅
                </button>
                <button onClick={() => formatText('justifyCenter')} className="format-btn" title="Align Center">
                  ↔
                </button>
                <button onClick={() => formatText('justifyRight')} className="format-btn" title="Align Right">
                  ➡
                </button>
                <button onClick={() => formatText('justifyFull')} className="format-btn" title="Justify">
                  ⬌
                </button>
              </div>

              <div className="format-group">
                <input 
                  type="color" 
                  onChange={(e) => formatText('foreColor', e.target.value)}
                  className="color-picker"
                  title="Text Color"
                />
                <input 
                  type="color" 
                  onChange={(e) => formatText('backColor', e.target.value)}
                  className="color-picker"
                  title="Background Color"
                />
              </div>

              <div className="format-group">
                <select 
                  onChange={(e) => formatText('fontName', e.target.value)}
                  className="font-select"
                  defaultValue=""
                >
                  <option value="">Font Family</option>
                  <option value="Arial">Arial</option>
                  <option value="Helvetica">Helvetica</option>
                  <option value="Times New Roman">Times New Roman</option>
                  <option value="Georgia">Georgia</option>
                  <option value="Verdana">Verdana</option>
                  <option value="Courier New">Courier New</option>
                </select>
                
                <select 
                  onChange={(e) => formatText('fontSize', e.target.value)}
                  className="font-size-select"
                  defaultValue=""
                >
                  <option value="">Size</option>
                  <option value="1">8pt</option>
                  <option value="2">10pt</option>
                  <option value="3">12pt</option>
                  <option value="4">14pt</option>
                  <option value="5">18pt</option>
                  <option value="6">24pt</option>
                  <option value="7">36pt</option>
                </select>
              </div>

              <div className="format-group">
                <button onClick={() => insertElement('table')} className="format-btn" title="Insert Table">
                  📊 Table
                </button>
                <button onClick={() => insertElement('image')} className="format-btn" title="Insert Image">
                  🖼️ Image
                </button>
                <button onClick={() => insertElement('link')} className="format-btn" title="Insert Link">
                  🔗 Link
                </button>
                <button onClick={() => insertElement('hr')} className="format-btn" title="Insert Horizontal Rule">
                  ➖ Line
                </button>
              </div>

              <div className="format-group">
                <button onClick={() => formatText('removeFormat')} className="format-btn" title="Clear Formatting">
                  🚫 Clear
                </button>
                <button onClick={() => formatText('undo')} className="format-btn" title="Undo">↶</button>
                <button onClick={() => formatText('redo')} className="format-btn" title="Redo">↷</button>
              </div>

              <div className="format-group">
                <button onClick={exportToPDF} className="format-btn" title="Export to PDF">
                  📄 PDF
                </button>
                <button onClick={exportToWord} className="format-btn" title="Export to Word">
                  📝 Word
                </button>
                <button onClick={printDocument} className="format-btn" title="Print">
                  🖨️ Print
                </button>
              </div>

              <div className="format-group">
                <button onClick={() => setShowFindReplace(!showFindReplace)} className="format-btn" title="Find & Replace">
                  🔍 Find
                </button>
                <button onClick={toggleSpellCheck} className={`format-btn ${showSpellCheck ? 'active' : ''}`} title={`Spell Check: ${showSpellCheck ? 'On' : 'Off'}`}>
                  ✓ Spell
                </button>
              </div>
            </div>

            {/* Find and Replace Panel */}
            {showFindReplace && (
              <div className="find-replace-panel" style={{
                padding: '15px',
                background: '#f8f9fa',
                border: '1px solid #ddd',
                borderRadius: '8px',
                margin: '10px 0',
                display: 'grid',
                gridTemplateColumns: '1fr 1fr auto auto',
                gap: '10px',
                alignItems: 'center'
              }}>
                <input
                  type="text"
                  placeholder="Find text..."
                  value={findText}
                  onChange={(e) => setFindText(e.target.value)}
                  style={{
                    padding: '8px 12px',
                    border: '1px solid #ddd',
                    borderRadius: '4px',
                    fontSize: '14px'
                  }}
                />
                <input
                  type="text"
                  placeholder="Replace with..."
                  value={replaceText}
                  onChange={(e) => setReplaceText(e.target.value)}
                  style={{
                    padding: '8px 12px',
                    border: '1px solid #ddd',
                    borderRadius: '4px',
                    fontSize: '14px'
                  }}
                />
                <button 
                  onClick={findAndHighlight}
                  className="format-btn"
                  style={{ background: '#007bff', color: 'white' }}
                >
                  Find
                </button>
                <button 
                  onClick={replaceAll}
                  className="format-btn"
                  style={{ background: '#28a745', color: 'white' }}
                >
                  Replace All
                </button>
              </div>
            )}

            {/* Document Statistics */}
            <div className="document-stats" style={{
              padding: '10px 15px',
              background: '#f8f9fa',
              border: '1px solid #e9ecef',
              borderRadius: '8px',
              margin: '10px 0',
              fontSize: '13px',
              color: '#6c757d',
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center'
            }}>
              <span>Words: <strong>{getWordCount()}</strong></span>
              <span>Characters: <strong>{getCharCount()}</strong></span>
              <span>Lines: <strong>{(editorRef.current?.textContent || '').split('\n').length}</strong></span>
            </div>

            {/* Editor Content */}
            <div className="editor-container">
              <div className="editor-content">
                <div
                  ref={editorRef}
                  className="rich-editor"
                  contentEditable={true}
                  suppressContentEditableWarning={true}
                  spellCheck={showSpellCheck}
                  onInput={handleContentChange}
                  onMouseUp={handleTextSelection}
                  onKeyUp={handleTextSelection}
                  onKeyDown={handleKeyDown}
                  onPaste={handlePaste}
                  style={{
                    minHeight: '500px',
                    padding: '20px',
                    border: '1px solid #e9ecef',
                    borderRadius: '8px',
                    backgroundColor: 'white',
                    lineHeight: '1.6',
                    fontSize: '16px',
                    fontFamily: 'Georgia, serif',
                    outline: 'none',
                    whiteSpace: 'pre-wrap',
                    wordWrap: 'break-word'
                  }}
                  data-placeholder="Start writing your document..."
                />

                {/* Comment Panel */}
                {selectedText && (
                  <div className="comment-panel">
                    <div className="selected-text">"{selectedText}"</div>
                    <textarea
                      value={newComment}
                      onChange={(e) => setNewComment(e.target.value)}
                      placeholder="Add a comment..."
                      rows={3}
                    />
                    <div className="comment-actions">
                      <button onClick={addComment} disabled={!newComment.trim()}>
                        Add Comment
                      </button>
                      <button onClick={() => setSelectedText('')}>Cancel</button>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </>
        ) : (
          <div className="no-document">
            <div className="no-doc-content">
              <h3>Select a document to start editing</h3>
              <p>Choose a document from the sidebar or create a new one to begin collaborating.</p>
              <button 
                className="create-first-doc"
                onClick={() => setShowNewDocModal(true)}
              >
                Create New Document
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Comments Sidebar */}
      {showComments && currentDocument && (
        <div className="comments-sidebar">
          <div className="comments-header">
            <h3>Comments</h3>
            <button 
              className="close-comments"
              onClick={() => setShowComments(false)}
            >
              ×
            </button>
          </div>

          <div className="comments-list">
            {documentComments.length === 0 ? (
              <div className="no-comments">
                <p>No comments yet. Select text and add a comment to start the conversation.</p>
              </div>
            ) : (
              documentComments.map(comment => (
                <div key={comment.id} className="comment">
                  <div className="comment-header">
                    <span className="comment-author">{comment.author}</span>
                    <span className="comment-time">
                      {comment.timestamp.toLocaleDateString()}
                    </span>
                  </div>
                  <div className="comment-text">"{comment.selectedText}"</div>
                  <div className="comment-content">{comment.content}</div>
                  <div className="comment-actions">
                    <button 
                      onClick={() => resolveComment(comment.id)}
                      className="resolve-btn"
                    >
                      Resolve
                    </button>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      )}

      {/* New Document Modal */}
      {showNewDocModal && (
        <div className="modal-overlay">
          <div className="modal">
            <div className="modal-header">
              <h3>Create New Document</h3>
              <button 
                className="close-modal"
                onClick={() => setShowNewDocModal(false)}
              >
                ×
              </button>
            </div>

            <div className="modal-content">
              <div className="form-group">
                <label>Document Title</label>
                <input
                  type="text"
                  value={newDocTitle}
                  onChange={(e) => setNewDocTitle(e.target.value)}
                  placeholder="Enter document title..."
                />
              </div>

              <div className="form-group">
                <label>Template</label>
                <div className="template-options">
                  {Object.entries(documentTemplates).map(([key, template]) => (
                    <label key={key} className="template-option">
                      <input
                        type="radio"
                        name="template"
                        value={key}
                        checked={newDocTemplate === key}
                        onChange={(e) => setNewDocTemplate(e.target.value)}
                      />
                      <div className="template-info">
                        <div className="template-name">{template.title}</div>
                      </div>
                    </label>
                  ))}
                </div>
              </div>
            </div>

            <div className="modal-actions">
              <button 
                onClick={() => setShowNewDocModal(false)}
                className="cancel-btn"
              >
                Cancel
              </button>
              <button 
                onClick={createDocument}
                className="create-btn"
                disabled={!newDocTitle.trim()}
              >
                Create Document
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default DocumentEditor;
