import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useSocket } from '../contexts/SocketContext';
import './Dashboard.css';

const Dashboard = () => {
  const { user } = useAuth();
  const { isConnected, activeUsers } = useSocket();
  const navigate = useNavigate();
  const [stats, setStats] = useState({
    totalProjects: 0,
    activeTasks: 0,
    completedTasks: 0,
    teamMembers: 0
  });
  const [recentActivity, setRecentActivity] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Simulate loading dashboard data
    const loadDashboardData = async () => {
      try {
        // In a real app, you would fetch this data from your API
        await new Promise(resolve => setTimeout(resolve, 1000));
        
        setStats({
          totalProjects: 12,
          activeTasks: 34,
          completedTasks: 128,
          teamMembers: 24
        });

        setRecentActivity([
          {
            id: 1,
            type: 'task_completed',
            message: 'Sarah completed "Design wireframes"',
            time: '2 minutes ago',
            icon: '✅'
          },
          {
            id: 2,
            type: 'project_created',
            message: 'New project "Mobile App Redesign" created',
            time: '1 hour ago',
            icon: '📁'
          },
          {
            id: 3,
            type: 'idea_added',
            message: 'John added a new idea to brainstorming board',
            time: '3 hours ago',
            icon: '💡'
          },
          {
            id: 4,
            type: 'document_updated',
            message: 'Project requirements document updated',
            time: '5 hours ago',
            icon: '📄'
          },
          {
            id: 5,
            type: 'team_joined',
            message: 'Alex joined the design team',
            time: '1 day ago',
            icon: '👤'
          }
        ]);
      } catch (error) {
        console.error('Error loading dashboard data:', error);
      } finally {
        setLoading(false);
      }
    };

    loadDashboardData();
  }, []);

  // Navigation handlers for Quick Actions
  const handleCreateProject = () => {
    navigate('/projects');
  };

  const handleJoinMeeting = () => {
    navigate('/chat');
  };

  const handleStartWhiteboard = () => {
    navigate('/whiteboard');
  };

  const handleBrowseIdeas = () => {
    navigate('/whiteboard'); // You can change this to a dedicated ideas page
  };

  const handleSeeAllProjects = () => {
    navigate('/projects');
  };

  const handleViewAnalytics = () => {
    navigate('/analytics');
  };

  const handleProjectClick = (projectId) => {
    navigate(`/kanban/${projectId || 'project1'}`);
  };

  const handleAIGenerateReport = () => {
    navigate('/analytics');
  };

  const handleAIBrainstorm = () => {
    navigate('/whiteboard');
  };

  const handleAIOptimizeSchedule = () => {
    navigate('/projects');
  };

  const getGreeting = () => {
    const hour = new Date().getHours();
    if (hour < 12) return 'Good morning';
    if (hour < 18) return 'Good afternoon';
    return 'Good evening';
  };

  if (loading) {
    return (
      <div className="dashboard-loading">
        <div className="loading-spinner large"></div>
        <p>Loading your dashboard...</p>
      </div>
    );
  }

  return (
    <div className="dashboard">
      <div className="dashboard-header">
        <div className="welcome-section">
          <h1>{getGreeting()}, {user?.name}! 👋</h1>
          <p>Here's what's happening with your projects today.</p>
        </div>
        
        <div className="connection-status">
          <div className={`status-indicator ${isConnected ? 'connected' : 'disconnected'}`}>
            <span className="status-dot"></span>
            {isConnected ? 'Connected' : 'Disconnected'}
          </div>
          {activeUsers.length > 0 && (
            <div className="active-users">
              <span className="users-count">{activeUsers.length}</span>
              <span className="users-text">online</span>
            </div>
          )}
        </div>
      </div>

      {/* Stats Grid */}
      <div className="stats-grid">
        <div className="stat-card projects">
          <div className="stat-icon">📁</div>
          <div className="stat-content">
            <h3>{stats.totalProjects}</h3>
            <p>Active Projects</p>
          </div>
          <div className="stat-trend">+2 this month</div>
        </div>

        <div className="stat-card tasks">
          <div className="stat-icon">📋</div>
          <div className="stat-content">
            <h3>{stats.activeTasks}</h3>
            <p>Pending Tasks</p>
          </div>
          <div className="stat-trend">-5 since yesterday</div>
        </div>

        <div className="stat-card completed">
          <div className="stat-icon">✅</div>
          <div className="stat-content">
            <h3>{stats.completedTasks}</h3>
            <p>Completed Tasks</p>
          </div>
          <div className="stat-trend">+12 this week</div>
        </div>

        <div className="stat-card team">
          <div className="stat-icon">👥</div>
          <div className="stat-content">
            <h3>{stats.teamMembers}</h3>
            <p>Team Members</p>
          </div>
          <div className="stat-trend">+3 new members</div>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="quick-actions">
        <h2>Quick Actions</h2>
        <div className="actions-grid">
          <button className="action-button create-project" onClick={handleCreateProject}>
            <span className="action-icon">➕</span>
            <span className="action-text">Create Project</span>
          </button>
          
          <button className="action-button join-meeting" onClick={handleJoinMeeting}>
            <span className="action-icon">🎥</span>
            <span className="action-text">Join Meeting</span>
          </button>
          
          <button className="action-button start-whiteboard" onClick={handleStartWhiteboard}>
            <span className="action-icon">🎨</span>
            <span className="action-text">Start Whiteboard</span>
          </button>
          
          <button className="action-button browse-ideas" onClick={handleBrowseIdeas}>
            <span className="action-icon">💡</span>
            <span className="action-text">Browse Ideas</span>
          </button>
        </div>
      </div>

      {/* Content Grid */}
      <div className="dashboard-content">
        {/* Recent Projects */}
        <div className="content-section">
          <div className="section-header">
            <h2>Recent Projects</h2>
            <button className="see-all-btn" onClick={handleSeeAllProjects}>See All</button>
          </div>
          <div className="projects-list">
            <div className="project-item" onClick={() => handleProjectClick('website-redesign')} style={{cursor: 'pointer'}}>
              <div className="project-avatar">🚀</div>
              <div className="project-info">
                <h4>Website Redesign</h4>
                <p>8 members • 12 tasks • Due in 5 days</p>
                <div className="progress-bar">
                  <div className="progress-fill" style={{width: '75%'}}></div>
                </div>
              </div>
              <div className="project-status active">Active</div>
            </div>

            <div className="project-item" onClick={() => handleProjectClick('mobile-app')} style={{cursor: 'pointer'}}>
              <div className="project-avatar">📱</div>
              <div className="project-info">
                <h4>Mobile App Development</h4>
                <p>6 members • 23 tasks • Due in 12 days</p>
                <div className="progress-bar">
                  <div className="progress-fill" style={{width: '45%'}}></div>
                </div>
              </div>
              <div className="project-status active">Active</div>
            </div>

            <div className="project-item" onClick={() => handleProjectClick('brand-identity')} style={{cursor: 'pointer'}}>
              <div className="project-avatar">🎨</div>
              <div className="project-info">
                <h4>Brand Identity</h4>
                <p>4 members • 7 tasks • Due in 3 days</p>
                <div className="progress-bar">
                  <div className="progress-fill" style={{width: '90%'}}></div>
                </div>
              </div>
              <div className="project-status review">Review</div>
            </div>
          </div>
        </div>

        {/* Recent Activity */}
        <div className="content-section">
          <div className="section-header">
            <h2>Recent Activity</h2>
            <button className="see-all-btn" onClick={handleViewAnalytics}>See All</button>
          </div>
          <div className="activity-feed">
            {recentActivity.map(activity => (
              <div key={activity.id} className="activity-item">
                <div className="activity-icon">{activity.icon}</div>
                <div className="activity-content">
                  <p>{activity.message}</p>
                  <span className="activity-time">{activity.time}</span>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* AI Assistant */}
      <div className="ai-assistant">
        <div className="assistant-header">
          <h3>🤖 AI Assistant</h3>
          <button className="minimize-btn">−</button>
        </div>
        <div className="assistant-content">
          <p>Hi! I'm here to help you with project insights and suggestions.</p>
          <div className="assistant-suggestions">
            <button className="suggestion-chip" onClick={handleAIGenerateReport}>📊 Generate project report</button>
            <button className="suggestion-chip" onClick={handleAIBrainstorm}>💡 Brainstorm ideas</button>
            <button className="suggestion-chip" onClick={handleAIOptimizeSchedule}>📅 Optimize schedule</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
